<template>
  <div v-if="dialogVisible" class="pxy-dialog">
    <div class="pxy-dialog-box">
      <el-row type="flex" justify="end">
        <i @click="closeDialog" class="el-icon-circle-close" style="font-size: 25px;cursor: pointer;"></i>
      </el-row>
      <div class="setting-tab">
        <el-tabs v-model="activeName">
          <el-tab-pane label="个人名片" name="callingCard"></el-tab-pane>
          <el-tab-pane label="头顶信息" name="topInfo"></el-tab-pane>
          <el-tab-pane label="视角切换" name="angleChange"></el-tab-pane>
        </el-tabs>
      </div>
      <!-- 个人名片 -->
      <div class="calling-card" v-if="activeName === 'callingCard'">
        <div>
          <span>头像：</span>
          <div>
            <div>
              <el-image style="width:35px;height: 35px;" :src="memberInfo.avatar"></el-image>
            </div>
            <p style="color: #fff; border-bottom: 1px solid #fff;cursor: pointer;display: inline-block" @click="avatarChange">换一个</p>
          </div>
        </div>
        <div>
          <span>姓名：</span>
          <div>{{ memberInfo.name }}</div>
        </div>
        <div>
          <span>性别：</span>
          <div>{{ memberInfo.sex === 1? '男':(memberInfo.sex === 0? '女': '') }}</div>
        </div>
        <div>
          <span>公司：</span>
          <div>{{ memberInfo.company }}</div>
        </div>
        <div>
          <span>职务：</span>
          <div>{{ memberInfo.position }}</div>
        </div>
        <div>
          <span>手机：</span>
          <div>{{ memberInfo.phone }}</div>
        </div>
        <div>
          <span>微信：</span>
          <div>{{ memberInfo.wechat }}</div>
        </div>
        <div>
          <span>邮箱：</span>
          <div>{{ memberInfo.email }}</div>
        </div>
        <div>
          <span>兴趣标签：</span>
          <div>
            <template v-for="(item, index) in memberInfo.labels">
              <span>{{ item.label }}</span>
            </template>
          </div>
        </div>
      </div>
      <!-- 头顶信息 -->
      <div class="top-info" v-if="activeName === 'topInfo'">
        <div>
          <span>头顶信息：</span>
          <span>可选择想要展示到头顶信息，并可选择开启或关闭头顶信息</span>
        </div>
        <div>
          <span>头顶信息开关：</span>
          <el-switch @change="topInfoChange" :active-value="1" :inactive-value="0" v-model="topInfo.switch"  active-color="#fff" inactive-color="#fff"></el-switch>
        </div>
        <el-divider></el-divider>
        <div>
          <span>头顶信息选项：</span>
        </div>
        <div>
          <p><el-checkbox @change="topInfoChange" v-model="topInfo.userName">姓名</el-checkbox></p>
          <p><el-checkbox @change="topInfoChange" v-model="topInfo.company">公司</el-checkbox></p>
          <p><el-checkbox @change="topInfoChange" v-model="topInfo.position">职务</el-checkbox></p>
        </div>
      </div>
      <!-- 视角切换 -->
      <div class="angle-change" v-if="activeName === 'angleChange'">
        <div style="margin-bottom: 20px;">
          <span>视角切换：</span>
          <span>默认第三人称视角，可切换为第一人称视角</span>
        </div>
        <div>
          <span style="margin-bottom: 20px;">视角切换：</span>
        </div>
        <div class="angle-change-switch">
          <p>
            <span>第三人称</span>
            <el-switch @change="angleChange" v-model="topInfo.switch" :active-value="3" :inactive-value="1" active-color="#fff" inactive-color="#fff"></el-switch>
          </p>
          <p>
            <span>第一人称</span>
            <el-switch @change="angleChange" v-model="topInfo.switch" :active-value="1" :inactive-value="3"  active-color="#fff" inactive-color="#fff"></el-switch>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { memberInfo } from '@/apis/api'
export default {
  props: {
    'dialogVisible': Boolean,
    'closeIcon': {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      activeName: 'callingCard', // 当前tab页
      topInfo: {
        switch: 3,
        userName: true,
        company: true,
        position: true
      },
      memberInfo: {}
    }
  },
  computed: {
    larksr() {
      return this.$store.state.larksr
    }
  },
  watch: {
    'dialogVisible': {
      handler(newValue) {
        if(newValue) {
          this.getMemberInfo();
        }
      }
    }
  },
  methods: {
    closeDialog() {
      this.$emit('closeDialog')
    },
    angleChange(val) {
      this.topInfo.switch = val;
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          scope: 2,
          type: 18,
          token: JSON.parse(this.$store.state.userInfo).token,
          data: {
            value: this.topInfo.switch
          }
        })
      )
    },
    // 头像换一个
    avatarChange() {
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          scope: 2,
          type: 4,
          token: JSON.parse(this.$store.state.userInfo).token
        })
      )
    },
    // 获取个人信息
    getMemberInfo() {
      const params = {
        memberId: this.$store.state.memberId
      }
      memberInfo(params).then(res => {
        if(res && res.code === 1000) {
          this.memberInfo = res.result;
        }
      })
    },
    // 头顶信息
    topInfoChange() {
      let labels = [];
      if(this.topInfo.userName) labels.push('userName');
      if(this.topInfo.company) labels.push('company');
      if(this.topInfo.position) labels.push('position');
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          scope: 2,
          type: 5,
          token: JSON.parse(this.$store.state.userInfo).token,
          data: {
            status: this.topInfo.switch,
            labels: labels
          }
        })
      )
    }
  }
}
</script>
<style lang='scss' scoped>
 .pxy-dialog {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   color: #fff;
   z-index: 2;
   &-box {
     width: 906px;
     min-height: 500px;
     padding: 20px 30px;
     border-radius: 8px;
     background-color: #2e2e2e99;
     .setting-tab {
       /deep/.el-tabs__item {
         color: #fff!important;
       }
       /deep/.el-tabs__item.is-active {
         color: #32cdcd!important;
       }
     }
     .calling-card {
       font-size: 14px;
       >div {
         margin: 13px 0;
         >span {
           display: inline-block;
           width: 80px;
           text-align: right;
           margin-right: 20px;
           vertical-align: middle;
           font-weight: 600;
         }
         >div {
           display: inline-block;
           vertical-align: middle;
           font-size:12px;
           >div {
             width: 35px;
             height: 35px;
             display: inline-block;
             vertical-align: middle;
             margin-right: 30px;
             border-radius: 50%;
             overflow: hidden;
           }
          //  兴趣标签
           >span {
            display: inline-block;
            padding: 5px 30px;
            border-radius: 8px;
            border: 1px solid #cccccc66;
            background: #acacac33;
            box-shadow: 2px 4px 4px #0000000d;
            margin: 10px 15px 10px 0;
           }
         }
       }
     }
     .top-info {
       color: #fff;
       font-size: 14px;
       >div>span:nth-of-type(1) {
          font-weight: 600;
       }
       >div:nth-of-type(1) {
         margin-bottom: 20px;
       }
       >div:nth-of-type(1),>div:nth-of-type(2) {
         >span:nth-of-type(1) {
           display: inline-block;
           margin-right: 20px;
         }
         >span:nth-of-type(2) {
           font-size: 12px;
         }
       }
       >div:nth-of-type(5) {
         >p {
           line-height: 35px;
         }
         /deep/.el-checkbox {
           color: #fff!important;
         }
         /deep/.el-checkbox__inner {
          background-color: #7d7d80!important;
         }
         /deep/.el-checkbox.is-checked {
           >.el-checkbox__label {
            color: #fff!important;
          }
         }
         /deep/.el-checkbox__input.is-checked .el-checkbox__inner {
           background-color: #7d7d80!important;
           border-color: #fff;
         }
         /deep/.el-checkbox__input.is-focus .el-checkbox__inner {
           border-color: #fff;
         }
         /deep/.el-checkbox__input .el-checkbox__inner:hover {
           border-color: #fff;
         }
         /deep/.el-checkbox__input.is-checked .el-checkbox__label {
           color: #fff!important;
         }
       }
     }
     .angle-change {
       color: #fff;
       font-size: 14px;
       >div>span:nth-of-type(1) {
          font-weight: 600;
       }
       >div{
         >span {
         display: inline-block;
         margin-right: 20px;
        }
        >span:nth-of-type(2) {
          font-size: 12px;
        }
       }
       &-switch {
         >p {
           margin-bottom: 20px;
           >span {
             display: inline-block;
             margin-right: 5px;
             font-size: 12px;
           }
         }
       }
     }
   }
  /deep/.el-switch {
    >span {
      background-color: #2e2e2e99!important;
    }
  }
  /deep/.el-switch.is-checked .el-switch__core {
    background-color: #fff!important;
  }
  /deep/.el-switch.is-checked .el-switch__core:after {
    background-color: #2e2e2e99!important;
  }
 }
</style>