<template>
  <div class="enter-appli-left-bar-box">
    <!-- 左侧工具栏 -->
    <div class="enter-appli-left-bar">
      <div class="logo"><el-image class="logo-img" :src="logoUrl"></el-image></div>
      <div class="tools-row">
        <div class="tools-box">
          <div @click="mapTools('map')">
            <div :class="{'tools-box-active':leftToolsText==='map'}"><el-image :src="require('@/assets/images/barIcon01.png')"></el-image></div>
            <span>地图</span>
          </div>
          <div @click="leftTools('course')">
            <div :class="{'tools-box-active':leftToolsText==='course'}"><el-image :src="require('@/assets/images/barIcon02.png')"></el-image></div>
            <span>教程</span>
          </div>
          <div @click="noticeTools('notice')">
            <div :class="{'tools-box-active':leftToolsText==='notice'}"><el-image :src="require('@/assets/images/barIcon03.png')"></el-image></div>
            <span>公告</span>
          </div>
          <div @click="leftTools('transfer')">
            <div :class="{'tools-box-active':leftToolsText==='transfer'}"><el-image :src="require('@/assets/images/barIcon04.png')"></el-image></div>
            <span>传送</span>
          </div>
          <div @click="leftTools('social')">
            <el-badge v-if="privateNoReadingTotal" :hidden="!privateNoReadingTotal" :value="privateNoReadingTotal">
              <div :class="{'tools-box-active':leftToolsText==='social'}"><el-image :src="require('@/assets/images/barIcon05.png')"></el-image></div>
            </el-badge>
            <el-badge v-else :hidden="!groupNoReadingTotal" is-dot>
              <div :class="{'tools-box-active':leftToolsText==='social'}"><el-image :src="require('@/assets/images/barIcon05.png')"></el-image></div>
            </el-badge>
            <span>社交</span>
          </div>
          <div @click="leftTools('backpack')">
            <div :class="{'tools-box-active':leftToolsText==='backpack'}"><el-image :src="require('@/assets/images/barIcon06.png')"></el-image></div>
            <span>背包</span>
          </div>
          <div @click="leftTools('setting')">
            <div :class="{'tools-box-active':leftToolsText==='setting'}"><el-image :src="require('@/assets/images/barIcon07.png')"></el-image></div>
            <span>设置</span>
          </div>
        </div>
        <div class="tools-box">
          <div @click="leftTools('logout')">
            <div :class="{'tools-box-active':leftToolsText==='logout'}"><el-image :src="require('@/assets/images/barIcon08.png')"></el-image></div>
            <!-- <el-button type="primary" size="mini" round @click="shareFn">共享</el-button> -->
            <span>退出</span>
          </div>
        </div>
      </div>
    </div>
    <!-- 新手教程 -->
    <div v-if="stepOne==='one' && isCourse" class="step-box">
      <el-row type="flex" justify="center">
        <el-image style="margin: 0 auto;width: 168px;" :src="require('@/assets/images/wasd.png')"></el-image>
      </el-row>
      <p>点击键盘【WASD】可移动人物</p>
      <el-row type="flex" justify="center">
        <el-button plain round class="primary-color" size="small" style="padding: 8px 30px;" @click="courseClick('two')">下一步</el-button>
      </el-row>
    </div>
    <div v-if="stepOne==='two' && isCourse" class="step-box">
      <el-row type="flex" justify="center">
        <el-image style="margin: 0 auto;width: 100px;" :src="require('@/assets/images/mouse.png')"></el-image>
      </el-row>
      <p>长按鼠标左键，左右拖动，可切换左右视/抬头或低头</p>
      <el-row type="flex" justify="center">
        <el-button plain round class="primary-color" size="small" style="padding: 8px 30px;" @click="courseClick('three')">下一步</el-button>
      </el-row>
    </div>
    <div v-if="stepOne==='three' && isCourse" class="step-box">
      <el-row type="flex" justify="center">
        <el-image style="margin: 0 auto;width: 100px;" :src="require('@/assets/images/track.png')"></el-image>
      </el-row>
      <p>点击要到达的地面位置，人物可自己走动到指定位置</p>
      <el-row type="flex" justify="center">
        <el-button plain round class="primary-color" size="small" style="padding: 8px 30px;" @click="courseClick('')">下一步</el-button>
      </el-row>
    </div>
    <!--新手教程结束弹窗  -->
    <DialogVue :dialogVisible="courseEndDialogVisible&&leftToolsText==='course'" :closeIcon="false">
      <div class="course-dialog">
        <p>恭喜您，已完成新手教学</p>
        <p>若有操作问题，可在左侧到“教程”中随时查看，马上开启您的神秘之旅吧！</p>
        <el-button type="info" round size="mini" style="padding: 7px 40px;" @click="courseClose">开启</el-button>
      </div>
    </DialogVue>
    <!-- 新手教程 -->
    <CourseVue :dialogVisible="courseDialogVisible&&leftToolsText==='course'" @closeCourseDialog="closeCourseDialogFn" />
    <!-- 公告 -->
    <DialogVue :dialogVisible="noticeDialogVisible&&leftToolsText==='notice'" @closeDialog="noticeCloseDialogFn" >
      <div class="notice-dialog">
        <p>公告</p>
        <p>{{ noticeInfo.text }}</p>
      </div>
    </DialogVue>
    <!-- 传送回出生地、会议通知 -->
    <DialogVue :dialogVisible="transferDialogVisible&&leftToolsText==='transfer'" @closeDialog="transferCloseDialogFn" >
      <div class="transfer-dialog">
        <template>
          <p>传送回出生地</p>
          <p>遇到卡图、迷路、或想迅速回到出生地时，点击【传送】可立刻送回出生地，是否需要传送？</p>
        </template>
        <!-- <template>
          <p>会议通知</p>
          <p>距大会开始海域5分钟，建议您尽快赶到会场就坐，或选择传送回出生地，是否立刻传送回出生地？</p>
        </template> -->
        <el-row>
          <el-button type="info" round size="mini" style="padding: 7px 40px;background:#4e4e4f;" @click="transferCloseDialogFn">取消</el-button>
          <el-button type="info" round size="mini" style="padding: 7px 40px;margin-left: 40px;background: #32cdcd;border: none;" @click="transferEnsure" >传送</el-button>
        </el-row>
      </div>
    </DialogVue>
    <!-- 退出会 -->
    <DialogVue :dialogVisible="logoutDialogVisible&&leftToolsText==='logout'" @closeDialog="logoutCloseDialogFn" >
      <div class="transfer-dialog">
        <p>退出会</p>
        <p>确定要离开会议吗？离开后可再次进入。</p>
        <el-row>
          <el-button type="info" round size="mini" style="padding: 7px 40px;background:#4e4e4f;" @click="logoutCloseDialogFn">再想想</el-button>
          <el-button type="primary" round size="mini" style="padding: 7px 40px;margin-left: 40px;" @click="sureLogout">忍痛离开</el-button>
        </el-row>
      </div>
    </DialogVue>
    <!-- 社交 -->
    <ChatVue
      :dialogVisible="socialDialogVisible&&leftToolsText==='social'"  
      @closeDialog="socialCloseDialogFn"
      @applyChangeCard="applyChangeCardFn"
    />
    <!-- 背包 -->
    <BackpackVue :dialogVisible="backpackDialogVisible&&leftToolsText==='backpack'" @closeDialog="backpackCloseDialogFn" />
    <!-- 设置 -->
    <SettingVue :dialogVisible="settingDialogVisible&&leftToolsText==='setting'" @closeDialog="settingCloseDialogFn" />
  </div>
</template>
<script>
import DialogVue from './components/Dialog.vue'
import CourseVue from './components/Course.vue'
import SettingVue from './components/Setting.vue'
import BackpackVue from './components/Backpack.vue'
import ChatVue from './components/Chat.vue'
import { getNotice } from '@/apis/api'
export default {
  components: { 
    DialogVue,
    CourseVue,
    SettingVue,
    BackpackVue,
    ChatVue
  },
  data() {
    return {
      logoUrl: require('@/assets/images/logo.png'),
      isCourse: true, // 是否展示新手教程
      stepOne: '',// one two three步骤
      leftToolsText: '', // 左侧当前使用的工具
      courseEndDialogVisible: false, // 初始化新手教程结束弹窗
      courseDialogVisible: false, // 工具栏新手教程弹窗
      noticeDialogVisible: false, // 公告弹窗
      transferDialogVisible: false, // 传送弹窗
      logoutDialogVisible: false, // 退出弹窗
      socialDialogVisible: false, //社交弹窗
      backpackDialogVisible: false, // 背包弹窗
      settingDialogVisible: false, // 设置弹窗
      noticeInfo: { // 公告内容
        text: ''
      },
      ws: null,
      timer: null
    }
  },
  computed: {
    larksr() {
      return this.$store.state.larksr
    },
    wsConnect() {
      return this.$store.state.ws
    },
    // 私聊未读数
    privateNoReadingTotal() {
      let total = 0;
      const privateList = JSON.parse(this.$store.state.chatModule.privateList);
      if(privateList && privateList.length>0) {
        privateList.forEach(item => {
          if(item.noReading) {
            total += item.noReading
          }
        })
      }
      return total
    },
    //群聊未读数
    groupNoReadingTotal() {
      return this.$store.state.chatModule.groupChatNoReading;
    }
  },
  methods: {
    // 新手教学步骤点击
    courseClick(val) {
      if (val) {
        this.stepOne = val
      } else {
        this.isCourse = false
        this.courseDialogVisible = true
      }
    },
    // 新手教学开启弹窗 点击关闭
    courseClose() {
      this.courseEndDialogVisible = false;
    },
    // 左侧工具栏新手教学弹窗关闭
    closeCourseDialogFn() {
      this.courseDialogVisible = false;
      this.leftToolsText = '';
    },
    // 左侧工具栏点击
    leftTools(val) {
      if(val === this.leftToolsText) {
        this.leftToolsText = ''
      } else {
        this.leftToolsText = val;
      }
      if(val === 'course') this.courseDialogVisible = true;
      if(val === 'course'&&this.leftToolsText === '') this.courseDialogVisible = false;
      if(val === 'transfer') this.transferDialogVisible = true;
      if(val === 'transfer'&&this.leftToolsText === '') this.transferDialogVisible = false;
      if(val === 'logout') this.logoutDialogVisible = true;
      if(val === 'logout'&&this.leftToolsText === '') this.logoutDialogVisible = false;
      if(val === 'setting') this.settingDialogVisible = true;
      if(val === 'setting'&&this.leftToolsText === '') this.settingDialogVisible = false;
      if(val === 'backpack') this.backpackDialogVisible = true;
      if(val === 'backpack'&&this.leftToolsText === '') this.backpackDialogVisible = false;
      if(val === 'social'){
        this.socialDialogVisible = true;
        this.$store.commit('chatModule/setGroupChatNoReading', false); // 清空群聊未读数
      } 
      if(val === 'social'&&this.leftToolsText === '') this.socialDialogVisible = false;
    },
    // 地图点击
    mapTools(val) {
      this.leftTools(val);
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          scope: 2,
          type: 16,
          token: JSON.parse(this.$store.state.userInfo).token,
          data: {
            status: this.leftToolsText === 'map'?1:0
          }
        })
      )
    },
    // 确认传送
    transferEnsure() {
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          scope: 2,
          type: 17,
          token: JSON.parse(this.$store.state.userInfo).token
        })
      )
      this.transferCloseDialogFn();
    },
    // 公告点击
    noticeTools(val) {
      this.leftTools(val);
      if(val === 'notice') this.noticeDialogVisible = true;
      if(val === 'notice' && this.leftToolsText === '') this.noticeDialogVisible = false;
      if(this.leftToolsText) {
        const params = {
          activityId: this.$store.state.activityId
        }
        getNotice(params).then(res => {
          if(res && res.code === 1000) {
            this.noticeInfo.text = res.result;
          }
        })
      }
    },
    // 退出会议
    sureLogout() {
      // 清除token信息，跳转到登录页
      window.localStorage.removeItem('userInfo');
      window.localStorage.removeItem('memberId');
      this.$store.commit('setUserInfo', null);
      this.$store.commit('setMemberId', null);
      this.$router.push('/login');
    },
    // 地图弹窗关闭
    closeMapBox() {
      this.leftToolsText = ''
    },
    // 公告弹窗关闭
    noticeCloseDialogFn() {
      this.leftToolsText = '';
      this.noticeDialogVisible = false;
    },
    // 传送弹窗关闭
    transferCloseDialogFn() {
      this.leftToolsText = '';
      this.transferDialogVisible = false;
    },
    // 退出弹窗关闭
    logoutCloseDialogFn() {
      this.leftToolsText = '';
      this.logoutDialogVisible = false;
    },
    // 社交弹窗关闭
    socialCloseDialogFn() {
      this.leftToolsText = '';
      this.socialDialogVisible = false;
      // 设置默认的聊天为群聊
      this.$store.commit('chatModule/setCurChatTab', 'group')
    },
    // 背包弹窗关闭
    backpackCloseDialogFn() {
      this.leftToolsText = '';
      this.backpackDialogVisible = false;
    },
    // 设置弹窗关闭
    settingCloseDialogFn() {
      this.leftToolsText = '';
      this.settingDialogVisible = false;
    },
    // 申请交换名片
    applyChangeCardFn(receiverUserId) {
      let message = {code:7,type:7,receiverUserId:receiverUserId}
      this.ws.send(JSON.stringify(message));
    },
    connectChat() {
      const _this = this;
      const memberId = this.$store.state.memberId;
      const activityId = this.$store.state.activityId;
      let ws = new WebSocket(`${window['config'].ZEUS_WS}?memberId=${memberId}&activityId=${activityId}`);
       _this.$store.commit('setWs', ws);
      this.ws = ws;
      ws.addEventListener('open',function(evt){
        console.log("Connection open ...  连接服务器成功");
        let message = {code:5,type:2,activityId:_this.$store.state.activityId}
        ws.send(JSON.stringify(message));
      })
      ws.addEventListener('message',function(evt){
        let data = JSON.parse(evt.data)
        if(data.code == 3){
          var message = {code:4,message:"pong",activityId:_this.$store.state.activityId}
          ws.send(JSON.stringify(message))
          return
        }
        // *************************************接收个人信息（聊天时本地插入数据时使用）
        if(data.code === 5 && data.type===17) {
          _this.$store.commit('setMemberInfo', JSON.stringify(data));
        }
        // *************************************底部操作按钮权限获取
        // 麦克风权限
        if(data.code === 5 && data.type===18) {
          if(Number(data.ext.status)===0) {// 禁用麦克风权限
            _this.$store.commit('setHasMicAuth', false);
             // 提示禁止发言
            _this.$store.commit('setShowHandUpBanTooltip', true)
            // 3s之后隐藏提示
            let showHandUpBanTooltipTimer = setTimeout(() => {
               _this.$store.commit('setShowHandUpBanTooltip', false);
               clearTimeout(showHandUpBanTooltipTimer)
            }, 3000)
          } else if(Number(data.ext.status)===1) { //允许使用麦克风
            _this.$store.commit('setHasMicAuth', true);
            // 提示允许发言
            _this.$store.commit('setShowHandUpTooltip', true)
            // 3s之后隐藏提示
            let showHandUpTooltipTimer = setTimeout(() => {
               _this.$store.commit('setShowHandUpTooltip', false);
               clearTimeout(showHandUpTooltipTimer)
            }, 3000)
          }
          if(data.ext.eventMsg) {
            _this.larksr?.sendTextToDataChannel(data.ext.eventMsg);
            const JSONData = JSON.parse(data.ext.eventMsg);
            console.log(JSONData)
            const status = JSONData.data.status;
            if(status === 0) { 
              // _this.$store.commit('setHasHandAuth', false);
            } else if(status === 1) {//允许发言了、放下举手
              _this.$store.commit('setHasHandAuth', false);
              // 举手图标也需要更换为放下
              _this.$emit('handDownEmit')
            }
          }
        }
        // 投屏权限
        if(data.code === 5 && data.type===19) {
          if(Number(data.ext.status)===0) {
            //收到管理员操作的关闭投屏操作，如果正在投屏就关闭投屏
            if(_this.$store.state.isShareScreen===true) {
              if(_this.$store.state.roomClient) {
                _this.$store.state.roomClient.disableShare();
              }
              _this.$store.commit('setIsShareScreen', false);
            }
            // 设置投屏权限为false
            _this.$store.commit('setHasScreenAuth', false);
            // 提示禁止投屏
            _this.$store.commit('setShowShareScreenBanTooltip', true)
            // 3s之后隐藏提示
            let showShareScreenBanTooltipTimer = setTimeout(() => {
               _this.$store.commit('setShowShareScreenBanTooltip', false);
               clearTimeout(showShareScreenBanTooltipTimer)
            }, 3000)
           
          } else if(Number(data.ext.status)===1) {
            _this.$store.commit('setHasScreenAuth', true);
             // 提示允许投屏
            _this.$store.commit('setShowShareScreenTooltip', true)
            // 3s之后隐藏提示
            let showShareScreenTooltipTimer = setTimeout(() => {
               _this.$store.commit('setShowShareScreenTooltip', false);
               clearTimeout(showShareScreenTooltipTimer)
            }, 3000)
          }
        }
        // 举手权限
        if(data.code === 5 && data.type===20) {
          if(Number(data.ext.status)===0) {
            _this.$store.commit('setHasHandAuth', false);
          } else if(Number(data.ext.status)===1) {
            _this.$store.commit('setHasHandAuth', true);
          }
        }
        // *************************************管理员面板 */
        if(data.code === 5 && data.type===4) {
          if(!data.ext) { return; }
          // 通知刷新人员列表
          if(data.ext.adminPanel.refreshMemberList && data.ext.adminPanel.refreshMemberList===1) {
             _this.$store.commit('chatModule/setRefreshMemberList', true)
          }
          // 存储管理员面板功能
          if(!data.ext.adminPanel.refreshMemberList) {
            const preAdminPanel = _this.$store.state.chatModule.adminPanel?JSON.parse(_this.$store.state.chatModule.adminPanel):{};
            _this.$store.commit('chatModule/setAdminPanel', JSON.stringify(Object.assign(preAdminPanel, data.ext.adminPanel)));
          }
        }
        // *************************************被管理员踢除了 */
        if(data.code === 5 && data.type === 16) {
          _this.$message.warning('您已被管理员踢除');
          setTimeout(() => {
            // 清除token信息，跳转到登录页
            window.localStorage.removeItem('userInfo');
            window.localStorage.removeItem('memberId');
            _this.$store.commit('setUserInfo', null);
            _this.$store.commit('setMemberId', null);
            _this.$router.push('/login');
          },1500)
        }
        // *************************************聊天消息 */
        // 接收私聊人员信息
        if(data.code === 5 && data.type===3) {
          if(data.ext) {
             const userList = JSON.stringify(data.ext.userList)
            _this.$store.commit('chatModule/setPrivateList', userList);
          }
        }
        // 接收私聊消息
        if(data.code === 1 && !data.type) {
          if(data.sendUserId === _this.$store.state.memberId) return;
          const userList = JSON.parse(_this.$store.state.chatModule.privateList);
          const index = userList?userList.findIndex(item => item.memberId === data.sendUserId):-1;
          if(data.message === '用户已离线') {
            console.log(_this.$store.state.chatModule.curPrivateMemberId)
            if(_this.$store.state.chatModule.curPrivateMemberId) {
              const ind = userList.findIndex(item => item.memberId === _this.$store.state.chatModule.curPrivateMemberId)
              userList[ind].chatRecords.push({
                ...data,
                sendUserId: _this.$store.state.chatModule.curPrivateMemberId
              })
            }
            _this.$store.commit('chatModule/setPrivateList', JSON.stringify(userList));
            return
          }
          // 直接向原有的私聊列表消息中推送
          if(index !== -1) {
            if(data.ext.noReading) { // 推送消息未读数字段
              if(_this.$store.state.chatModule.curChatTab==='group'||_this.$store.state.chatModule.curPrivateMemberId!==data.sendUserId)
              userList[index].noReading = data.ext.noReading;
              console.log(_this.$store.state.chatModule.curChatTab,_this.$store.state.chatModule.curPrivateMemberId, data.sendUserId)
              if(_this.$store.state.chatModule.curChatTab==='private'&&_this.$store.state.chatModule.curPrivateMemberId===data.sendUserId) {
                const message = {
                  code: 5,
                  type: 21,
                  receiverUserId: _this.$store.state.chatModule.curPrivateMemberId
                }
                _this.wsConnect?.send(JSON.stringify(message));
              }
            }
            userList[index].chatRecords.push(data)
          } else { // 私聊列表中插入一条新的
            userList.unshift({
              avatar: data.avatar,
              memberId: data.sendUserId,
              userName: data.userName,
              userTitle: data.userTitle,
              noReading: 1, // 消息未读数
              chatRecords: [data]
            })
          }
          _this.$store.commit('chatModule/setPrivateList', JSON.stringify(userList));
        }
        // 群聊新用户加入信息
        if(data.code === 5 && data.type===2) {
          const preUserList = JSON.parse(_this.$store.state.chatModule.userList)
          const index = preUserList.findIndex(item => item.memberId === data.sendUserId)
          if(index === -1) {
            preUserList.push(Object.assign(data, {
              memberId: data.sendUserId
            }))
            _this.$store.commit('chatModule/setUserList', JSON.stringify(preUserList));
          }
        }
        // 群聊用户下线
        if(data.code === 5 && data.type===1) {
          const preUserList = JSON.parse(_this.$store.state.chatModule.userList)
          const index = preUserList.findIndex(item => item.memberId === data.sendUserId);
          preUserList.splice(index,1)
          _this.$store.commit('chatModule/setUserList', JSON.stringify(preUserList));
        }
        //  接收群聊历史消息，按顺序铺排
        if(data.code === 2 && data.type===5) {
          if(data.ext.history) {
            const history = JSON.stringify(data.ext.history);
           _this.$store.commit('chatModule/setGroupHistory', history);
          }
          if(data.ext.userList) {
            const groupUserList = JSON.stringify(data.ext.userList);
            _this.$store.commit('chatModule/setUserList', groupUserList);
          }
        }
        // 群聊消息 
        if(data.code === 2 && !data.type) {
          const preHistory = JSON.parse(_this.$store.state.chatModule.groupHistory);
          if(data.sendUserId === _this.$store.state.memberId) return;
          // 设置群聊未读小红点
          if(_this.$store.state.chatModule.curChatTab!=='group' || _this.leftToolsText !=='social') {
            _this.$store.commit('chatModule/setGroupChatNoReading', true);
          }
          // 插入群聊消息
          preHistory.push(data)
          _this.$store.commit('chatModule/setGroupHistory', JSON.stringify(preHistory));
        }
        // 接收欢迎语，xxxx来了~ 系统消息形式展示
        if(data.code === 2 && data.type === 22) {
          const preHistory = JSON.parse(_this.$store.state.chatModule.groupHistory);
          if(data.sendUserId === _this.$store.state.memberId) return;
          // 设置群聊未读小红点
          if(_this.$store.state.chatModule.curChatTab!=='group' || _this.leftToolsText !=='social') {
            _this.$store.commit('chatModule/setGroupChatNoReading', true);
          }
          // 插入群聊消息
          preHistory.push(data)
          _this.$store.commit('chatModule/setGroupHistory', JSON.stringify(preHistory));
        }
       //*************************************发送名片 */
      //  点击申请交换名片 发送方收到事件
       if(data.code === 7 && data.type=== 8) {
        const prePrivateList = _this.$store.state.chatModule.privateList?JSON.parse(_this.$store.state.chatModule.privateList): [];
        const index = prePrivateList.findIndex(item => item.memberId === data.receiverUserId);
        // 向当前私聊用户推送
        prePrivateList[index].chatRecords.push({...data})
        _this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
       }
      //  点击申请交换名片 接收方收到事件
       if(data.code === 7 && data.type=== 9) {
        const prePrivateList = _this.$store.state.chatModule.privateList?JSON.parse(_this.$store.state.chatModule.privateList): [];
        const index = prePrivateList.findIndex(item => item.memberId === data.receiverUserId);
        if(index === -1) {
          // 私聊人员列表中没有这个用户，手动新增一个
          prePrivateList.push({
            avatar: data.avatar,
            memberId: data.memberId,
            userTitle: data.userTitle,
            userName: data.userName,
            noReading: 1, // 消息未读数
            chatRecords: [{...data}]
          })
        } else {
          // 向当前私聊用户推送
          prePrivateList[index].chatRecords.push({...data})
        }
        _this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
       }
       //  同意交换名片-发送方收到事件
      if(data.code === 7 && data.type=== 11) {
        const prePrivateList = _this.$store.state.chatModule.privateList?JSON.parse(_this.$store.state.chatModule.privateList): [];
        const index = prePrivateList.findIndex(item => item.memberId === data.receiverUserId);
        // 向当前私聊用户推送
        prePrivateList[index].chatRecords.push({...data})
        _this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
       }
      //  同意交换名片-接收方收到事件
      if(data.code === 7 && data.type=== 12) {
        const prePrivateList = _this.$store.state.chatModule.privateList?JSON.parse(_this.$store.state.chatModule.privateList): [];
        const index = prePrivateList.findIndex(item => item.memberId === data.receiverUserId);
        if(data.ext) {
          if(data.ext.msgId) {
            let msgIdIndex = prePrivateList[index].chatRecords.findIndex(item => item.id === data.ext.msgId);
            prePrivateList[index].chatRecords[msgIdIndex].ext.phone = data.ext.phone;
            prePrivateList[index].chatRecords[msgIdIndex].ext.wechat = data.ext.wechat;
            prePrivateList[index].chatRecords[msgIdIndex].ext.email = data.ext.email;
          }
        }
        // 向当前私聊用户推送
        prePrivateList[index].chatRecords.push({...data})
        _this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
       }
      //  拒绝交换名片-发送方收到事件
      if(data.code === 7 && data.type=== 14) {
        const prePrivateList = _this.$store.state.chatModule.privateList?JSON.parse(_this.$store.state.chatModule.privateList): [];
        const index = prePrivateList.findIndex(item => item.memberId === data.receiverUserId);
        // 向当前私聊用户推送
        prePrivateList[index].chatRecords.push({...data})
        _this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
       }
      //  拒绝交换名片-接收方收到事件
      if(data.code === 7 && data.type=== 15) {
        const prePrivateList = _this.$store.state.chatModule.privateList?JSON.parse(_this.$store.state.chatModule.privateList): [];
        const index = prePrivateList.findIndex(item => item.memberId === data.receiverUserId);
        // 向当前私聊用户推送
        prePrivateList[index].chatRecords.push({...data})
        _this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
       }
      })
    }
  },
  mounted() {
    this.timer = setTimeout(() => {this.connectChat()}, 1000);
  },
  unmounted() {
    if(this.timer) clearTimeout(timer);
  }
}
</script>
