<template>
<div>
  <div v-if="dialogVisible" class="pxy-chat-dialog">
    <el-row class="chat">
      <el-col :span="7" class="chat-left">
      <!-- @tab-click="handleClick" -->
        <el-tabs class="chat-group-person" v-model="chatTab" @tab-click="chatTabChange">
          <el-tab-pane name="group" style="position:relative">
            <span slot="label">
              <el-badge :hidden="!groupNoReadingTotal" is-dot>
                群聊
              </el-badge>
            </span>
            <el-input class="input-self" 
              placeholder="搜索" 
              prefix-icon="el-icon-search" 
              v-model="searchValue" size="mini" 
              @blur="searchGroupPerson" 
              @keyup.enter.native="searchGroupPerson"
              @clear="searchGroupPersonCancle"
              clearable
            ></el-input>
          </el-tab-pane>
          <el-tab-pane style="position:relative"  name="private">
            <span slot="label">
              私聊
              <span v-if="privateNoReadingTotal" class="private-total-badge">{{privateNoReadingTotal}}</span>
            </span>
          </el-tab-pane>
        </el-tabs>
        <!-- 群聊人员列表 -->
        <div v-if="chatTab==='group'" class="chat-person chat-group-person">
          <template v-for="(item,index) in groupChatList">
            <el-row type="flex">
              <div @click.prevent="groupPersonClick(item)">
                <el-image style="width:45px;height: 45px;" :src="item.avatar"></el-image>
              </div>
              <div>
                <p v-if="item.userTitle==='管理员'" class="tag-mini admin-tag-mini">管理员</p>
                <p v-if="item.userTitle==='观众'" class="tag-mini reporter-tag-mini">观众</p>
                <p v-if="item.userTitle==='VIP嘉宾'" class="tag-mini vip-tag-mini">VIP</p>
                <p v-if="item.userTitle==='演讲嘉宾'" class="tag-mini speaker-tag-mini">演讲嘉宾</p>
                <p v-else></p>
                <p class="chat-person-name">{{item.userName || ''}}</p>
              </div>
            </el-row>
          </template>
        </div>
        <!-- 私聊人员列表 -->
        <div v-if="chatTab==='private'" class="chat-person chat-private-person" style="margin-top: -15px;">
          <template v-for="(item,index) in privateChatList">
            <el-row type="flex" @click.native="privateChatRowClick(item, index)" :class="{'cur-private-chat': curPrivateIndex===index}">
              <div class="private-avatar">
                <span v-if="item.noReading" class="private-badge">{{item.noReading}}</span>
                <el-image style="width:45px;height: 45px;" :src="item.avatar"></el-image>
              </div>
              <div>
                <p v-if="item.userTitle==='管理员'" class="tag-mini admin-tag-mini">管理员</p>
                <p v-if="item.userTitle==='观众'" class="tag-mini reporter-tag-mini">观众</p>
                <p v-if="item.userTitle==='VIP嘉宾'" class="tag-mini vip-tag-mini">VIP</p>
                <p v-if="item.userTitle==='演讲嘉宾'" class="tag-mini speaker-tag-mini">演讲嘉宾</p>
                <p v-else></p>
                <p class="chat-person-name">{{item.userName || ''}}</p>
              </div>
            </el-row>
          </template>
        </div>
      </el-col>
      <el-col :span="17" class="chat-right">
      <!-- 顶部聊天信息 -->
        <el-row type="flex" justify="space-between" class="chat-top">
          <div v-if="chatTab==='group'">
            <span>群聊 </span>
            <span> {{`(${groupChatList?groupChatList.length:0})`}} </span>
          </div>
          <div v-if="chatTab==='private'">
            <span v-if="privateChatList[curPrivateIndex]">正在与<span>{{ privateChatList[curPrivateIndex].userName || ''}}</span>聊天 </span>
            <span v-else>私聊 </span>
          </div>
          <div>
            <!-- 当tab为私聊 && 私聊人员列表有数据 -->
            <el-tooltip v-if="privateChatList[curPrivateIndex]&&chatTab==='private'" class="item" effect="dark" placement="top" content="交换名片">
              <span class="iconfont icon-mingpian" style="font-size: 22px;cursor: pointer;margin-right: 15px;" @click="privateTopExchangeCard"></span>
            </el-tooltip>
            <!-- 关闭弹窗 -->
            <i class="el-icon-circle-close" style="font-size: 22px;cursor: pointer;" @click="closeDialog"></i>
          </div>
        </el-row>
        <!-- 群聊聊天内容 -->
        <el-scrollbar ref="groupScroller" v-show="chatTab==='group'" class="scroll-maxheight">
          <div class="chat-content">
            <div v-for="(item,index) in groupChatContentList">
              <p v-if="item.code===2&&item.type===22&&item.sendUserId !== memberId" class="notify-info">{{item.message}}</p>
              <template v-else>
                <!-- 自己输入内容 -->
                <template v-if="item.sendUserId === memberId">
                  <template v-if="item.type!==22">
                    <el-row type="flex" justify="end" class="chat-me">
                      <div>
                        <el-row type="flex" style="align-items:center;">
                          <span class="chat-me-time">{{timestampToTime(item.sendTime)}}</span>
                        </el-row>
                      </div>
                      <div>
                        <el-image style="width:35px;height: 35px;" :src="item.avatar"></el-image>
                      </div>
                    </el-row>
                    <el-row type="flex" justify="end">
                      <div class="chat-me-content">
                        <p>{{ item.message }}</p>
                      </div>
                    </el-row>
                  </template>
                </template>
                <!-- 其他人输入内容 -->
                <template v-else>
                  <el-row type="flex" class="chat-others">
                    <div style="border-radius: 50%;" @click.prevent="groupPersonClick(item)">
                      <el-image style="width:35px;height: 35px;" :src="item.avatar"></el-image>
                    </div>
                    <div>
                      <el-row type="flex" style="align-items:center;">
                        <p v-if="item.userTitle==='管理员'" class="tag-large admin-tag-mini">管理员</p>
                        <p v-if="item.userTitle==='观众'" class="tag-large reporter-tag-mini">观众</p>
                        <p v-if="item.userTitle==='VIP嘉宾'" class="tag-large vip-tag-mini">VIP</p>
                        <p v-if="item.userTitle==='演讲嘉宾'" class="tag-large speaker-tag-mini">演讲嘉宾</p>
                        <span class="chat-others-name">{{ item.userName }}</span>
                        <span class="chat-others-time">{{timestampToTime(item.sendTime)}}</span>
                      </el-row>
                    </div>
                  </el-row>
                  <div class="chat-others-content">
                    <p>{{ item.message }}</p>
                  </div>
                </template>
              </template>
            </div>
            <div ref="hiddenScroller" style="height: 40px"></div>
          </div>
        </el-scrollbar>
        <!-- 私聊聊天内容 -->
        <el-scrollbar ref="privateScroller" v-show="chatTab==='private'&&privateChatList" class="scroll-maxheight">
          <div class="chat-content" v-if="privateChatList[curPrivateIndex]">
            <div v-for="(item,index) in privateChatList[curPrivateIndex].chatRecords" :key="index">
              <p v-if="item.code===7&&item.type===14" class="notify-info">对方已拒绝交换名片</p>
              <p v-if="item.code===7&&item.type===12" class="notify-info">您已同意交互名片，您的名片已发给对方，同时对方名片已发送到您的名片夹中</p>
              <p v-if="item.code===7&&item.type===8" class="notify-info">您已申请交互名片，等待对方同意</p>
              <p v-if="item.code===7&&item.type===11" class="notify-info">对方已同意交互名片，对方名片已发送到您的名片夹中，同时对方您的名片也发送给对方</p>
              <p v-if="item.code===7&&item.type===15" class="notify-info">您已拒绝了对方的交互名片申请，名片不会发给对方</p>
              <template v-if="item.code===7&&item.type===9">
                <el-row v-if="item.message||item.ext" type="flex" class="chat-others">
                  <div style="border-radius: 50%;" @click.prevent="groupPersonClick(item)">
                    <el-image style="width:35px;height: 35px;" :src="item.avatar"></el-image>
                  </div>
                  <div>
                    <el-row type="flex" style="align-items:center;">
                      <p v-if="item.userTitle==='管理员'" class="tag-large admin-tag-mini">管理员</p>
                      <p v-if="item.userTitle==='观众'" class="tag-large reporter-tag-mini">观众</p>
                      <p v-if="item.userTitle==='VIP嘉宾'" class="tag-large vip-tag-mini">VIP</p>
                      <p v-if="item.userTitle==='演讲嘉宾'" class="tag-large speaker-tag-mini">演讲嘉宾</p>
                      <span class="chat-others-name">{{ item.userName || '' }}</span>
                      <span class="chat-others-time">{{timestampToTime(item.sendTime)}}</span>
                    </el-row>
                  </div>
                </el-row>
                <!-- 名片信息 -->
                <template v-if="item.code===7&&item.type===9">
                  <div class="chat-others-content">
                    <p class="calling-card-dialog-title">交换名片申请</p>
                    <div class="card" style="background:#515151;">
                      <p>{{ item.ext.company}}</p>
                      <div>
                        <span>{{ item.ext.userName}}</span>
                        <span>{{ item.ext.position}}</span>
                      </div>
                      <!--同意交换名片  -->
                      <template v-if="item.ext.agree===1">
                        <p>
                          <span class="iconfont icon-dianhuatianchong"></span>
                          <span>{{ item.ext.phone}}</span>
                        </p>
                        <p>
                          <span class="iconfont icon-weixin" style="font-size: 19px"></span>
                          <span>{{ item.ext.wechat}}</span>
                        </p>
                        <p>
                          <span class="iconfont icon-youxiang" style="font-size: 17px"></span>
                          <span>{{ item.ext.email}}</span>
                        </p>
                      </template>
                      <!-- 不同意/未点击过操作按钮 -->
                      <template v-else>
                        <p>
                          <span class="iconfont icon-dianhuatianchong"></span>
                          <span>******</span>
                        </p>
                        <p>
                          <span class="iconfont icon-weixin" style="font-size: 19px"></span>
                          <span>******</span>
                        </p>
                        <p>
                          <span class="iconfont icon-youxiang" style="font-size: 17px"></span>
                          <span>******</span>
                        </p>
                      </template>
                    </div>
                    <el-row v-if="item.ext.agree == undefined" type="flex" justify="center" style="margin-top: 18px;">
                      <el-button type="info" round size="mini" style="padding: 7px 35px;background:#4e4e4f;" @click="disagreeExchangeCard(item.id, index)">不同意</el-button>
                      <el-button type="info" round size="mini" style="padding: 7px 35px;" @click="agreeExchangeCard(item.id, index)">同意</el-button>
                    </el-row>
                  </div>
                </template>
              </template>
              <!-- 自己输入内容 -->
              <template v-if="item.sendUserId === memberId&&item.code!==7">
                <el-row v-if="item.message" type="flex" justify="end" class="chat-me">
                  <div>
                    <el-row type="flex" style="align-items:center;">
                      <span class="chat-me-time">{{timestampToTime(item.sendTime)}}</span>
                    </el-row>
                  </div>
                  <div>
                    <el-image style="width:35px;height: 35px;" :src="item.avatar"></el-image>
                  </div>
                </el-row>
                <el-row v-if="item.message" type="flex" justify="end">
                  <div class="chat-me-content">
                    <p>{{ item.message }}</p>
                  </div>
                </el-row>
              </template>
              <!-- 其他人输入内容 -->
              <template v-if="(item.sendUserId !== memberId)&&item.code!==7">
                <el-row v-if="item.message||item.ext" type="flex" class="chat-others">
                  <div style="border-radius: 50%;" @click.prevent="groupPersonClick(item)">
                    <el-image style="width:35px;height: 35px;" :src="item.avatar"></el-image>
                  </div>
                  <div>
                    <el-row type="flex" style="align-items:center;">
                      <p v-if="item.userTitle==='管理员'" class="tag-large admin-tag-mini">管理员</p>
                      <p v-if="item.userTitle==='观众'" class="tag-large reporter-tag-mini">观众</p>
                      <p v-if="item.userTitle==='VIP嘉宾'" class="tag-large vip-tag-mini">VIP</p>
                      <p v-if="item.userTitle==='演讲嘉宾'" class="tag-large speaker-tag-mini">演讲嘉宾</p>
                      <span class="chat-others-name">{{ item.userName }}</span>
                      <span class="chat-others-time">{{timestampToTime(item.sendTime)}}</span>
                    </el-row>
                  </div>
                </el-row>
                <div v-if="item.message" class="chat-others-content">
                  <p>{{ item.message }}</p>
                </div>
              </template>
            </div>
            <div ref="privateHiddenScroller" style="height: 40px"></div>
          </div>
        </el-scrollbar>
        <!-- 底部输入框 -->
        <el-row type="flex" class="chat-input">
          <el-input 
            ref="chatInput" 
            class="input-chat" 
            placeholder="说点什么" 
            prefix-icon="el-icon-edit" 
            v-model="chatValue" 
            @keyup.enter.native="sendChat" 
            clearable
            @focus="chatInputFocus"
            @blur="chatInputBlur"
          ></el-input>
          <picker class="emotion"
            v-show="showEmotion"
            :include="['people']"
            :showSearch="false"
            :showPreview="false" 
            :showCategories="false"
            @select="addEmoji"
            :backgroundImageFn="backgroundImageFn"
          />
          <div>
            <span class="iconfont icon-biaoqing2 chat-icon" style="color:#fff;" @click="showEmotionFn"></span>
            <span class="iconfont icon-fasong chat-icon" style="color: #32cdcd;" @click="sendChat"></span>
          </div>
        </el-row>
      </el-col>
    </el-row>
  </div>
    <!-- 交换名片弹窗 -->
    <div v-if="showCallingCardDialog" class="calling-card-dialog">
      <el-row type="flex" justify="end" style="padding: 10px 5px 0 0;">
        <i @click="closeCallingCardDialog" class="el-icon-circle-close" style="font-size: 25px;cursor: pointer;"></i>
      </el-row>
      <p v-if="exchangeCardInfo.hasExchange===1" class="calling-card-dialog-title" style="margin-bottom: 10px;">名片信息</p>
      <p v-else class="calling-card-dialog-title" style="margin-bottom: 10px;">交换名片申请</p>
      <el-row type="flex" justify="center">
        <div class="card">
            <p>{{ exchangeCardInfo.company }}</p>
            <template v-if="exchangeCardInfo.hasExchange===0">
              <div>
                <span>{{ exchangeCardInfo.userName }}</span>
                <span>{{ exchangeCardInfo.position }}</span>
              </div>
              <p>
                <span class="iconfont icon-dianhuatianchong"></span>
                <span>******</span>
              </p>
              <p>
                <span class="iconfont icon-weixin" style="font-size: 19px"></span>
                <span>******</span>
              </p>
              <p>
                <span class="iconfont icon-youxiang" style="font-size: 17px"></span>
                <span>******</span>
              </p>
            </template>
            <template  v-if="exchangeCardInfo.hasExchange===1">
              <div>
                <span>{{ exchangeCardInfo.userName }}</span>
                <span>{{ exchangeCardInfo.position }}</span>
              </div>
              <p>
                <span class="iconfont icon-dianhuatianchong"></span>
                <span>{{ exchangeCardInfo.phone }}</span>
              </p>
              <p>
                <span class="iconfont icon-weixin" style="font-size: 19px"></span>
                <span>{{ exchangeCardInfo.wechat }}</span>
              </p>
              <p>
                <span class="iconfont icon-youxiang" style="font-size: 17px"></span>
                <span>{{ exchangeCardInfo.email }}</span>
              </p>
            </template>
            
          </div>
      </el-row>
      <!-- 群聊交换名片 -->
      <el-row v-if="chatTab==='group'" type="flex" justify="center" style="margin-top: 18px;">
        <el-button type="info" round size="mini" style="padding: 7px 30px;background:#4e4e4f;" @click="wantPrivateChat">发起私聊</el-button>
        <el-button v-if="exchangeCardInfo.hasExchange===0" type="info" round size="mini" style="padding: 7px 30px;" @click="applyChangeCard">申请交换名片</el-button>
      </el-row>
      <!-- 私聊交换名片 -->
      <el-row v-if="chatTab==='private'" type="flex" justify="center" style="margin-top: 18px;">
        <!-- <el-button type="info" round size="mini" style="padding: 7px 30px;background:#4e4e4f;" @click="privateEchangeCancle">取消</el-button> -->
        <el-button v-if="exchangeCardInfo.hasExchange===0" type="info" round size="mini" style="padding: 7px 30px;" @click="privateApplyChangeCard">申请交换名片</el-button>
      </el-row>
    </div>
  </div>
</template>
<script>
import { getExchangeCard } from '@/apis/api'
import { Picker } from "emoji-mart-vue";
export default {
  components: {
    Picker
  },
  props: {
    'dialogVisible': Boolean,
    'closeIcon': {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      chatTab: 'group',
      searchValue: '',
      chatValue: '', // 聊天发送的内容
      groupChatListTotal: [], // 群聊列表，搜索人员时用来保存全部群聊人员
      groupChatList: [], // 群聊列表
      privateChatList: [], // 私聊列表
      groupChatContentList: [], // 群聊聊天内容
      showCallingCardDialog: false, // 名片弹窗
      curAvatarClickInfo: {}, // 当前点击头像的人物信息(群聊点击头像可发送消息或者申请交换名片)
      curPrivateIndex: 0, //  当前私聊人员的index
      curPrivateChat: {}, // 当前聊天的人
      exchangeCardInfo: {},//申请交换名片的信息
      showEmotion: false, // 表情弹窗
    }
  },
  computed: {
    wsConnect() {
      return this.$store.state.ws
    },
    memberInfo() {
      return this.$store.state.memberInfo? JSON.parse(this.$store.state.memberInfo): ''
    },
    memberId() {
      return this.$store.state.memberId
    },
    larksr() {
      return this.$store.state.larksr
    },
    // 私聊未读数的总数
    privateNoReadingTotal() {
      let total = 0;
      const privateList = JSON.parse(this.$store.state.chatModule.privateList);
      if(privateList && privateList.length>0) {
        privateList.forEach(item => {
          if(item.noReading) {
            total += item.noReading
          }
        })
      }
      return total
    },
    //群聊未读数
    groupNoReadingTotal() {
      return this.$store.state.chatModule.groupChatNoReading;
    }
  },
  watch: {
    'dialogVisible': {
      handler(newVal) {
        if(newVal) {
          this.$nextTick(() => {
            if(this.groupChatContentList.length) this.$refs.hiddenScroller.scrollIntoView({behavior: "auto"});
            if(this.privateChatList.length) this.$refs.privateHiddenScroller.scrollIntoView({behavior: "auto"});
          })
        }
      }
    },
    '$store.state.chatModule.userList': {
      handler(newVal, old) {
        this.$nextTick(() => {
          this.groupChatList = JSON.parse(newVal);
          this.groupChatListTotal = JSON.parse(newVal);
        })
      },
      // immediate: true,
      deep: true
    },
    '$store.state.chatModule.groupHistory': {
      handler(newVal, old) {
        this.$nextTick(() => {
          if(newVal) {
            this.groupChatContentList = JSON.parse(newVal);
             if(this.$refs.groupScroller){
              let div = this.$refs.groupScroller.$refs["wrap"];
              this.$nextTick(()=> {
                div.scrollTop = div.scrollHeight;
              })
             }
          }
        })
      },
      immediate: true,
      deep: true
    },
    '$store.state.chatModule.privateList': {
      handler(newVal, old) {
        this.$nextTick(() => {
          this.privateChatList = JSON.parse(newVal)
          if(this.$refs.privateScroller) {
            let div = this.$refs.privateScroller.$refs["wrap"];
            this.$nextTick(()=> {
              div.scrollTop = div.scrollHeight;
            })
          }
        })
      },
      immediate: true,
      deep: true
    },
  },
  methods: {
    // 表情的大图
    backgroundImageFn() {
      return require('@/assets/images/emo.png')
    },
    // 搜索群聊人员
    searchGroupPerson() {
      if(this.searchValue !=='') {
        let searchGroupList = []
        this.groupChatList.forEach(item => {
          if(item.userName.includes(this.searchValue)) {
            searchGroupList.push(item)
          }
        })
        this.groupChatList = searchGroupList;
      } else {
        this.groupChatList = this.groupChatListTotal;
      }
    },
    // 取消搜索群聊人员
    searchGroupPersonCancle() {
      this.searchValue = '';
      this.groupChatList = this.groupChatListTotal;
    },
    // 选择表情
    addEmoji(e) {
      this.chatValue += e.native;
      this.showEmotion = !this.showEmotion;
      this.$refs.chatInput.focus()
    },
    // 显示 隐藏表情框
    showEmotionFn() {
      this.showEmotion = !this.showEmotion;
    },
    // 发送聊天内容
    sendChat() {
      if(this.showEmotion) this.showEmotion = false;
      if(!this.chatValue) return;
      let message = {}
      // 群聊
      if(this.chatTab === 'group') {
        message = {
          code: 2,
          sendUserId: this.memberId,
          sendTime: new Date().getTime(),
          message: this.chatValue,
          activityId: this.$store.state.activityId,
          avatar: this.memberInfo?this.memberInfo.avatar:''
        }
        console.log('111')
        const preHistory = JSON.parse(this.$store.state.chatModule.groupHistory);
        preHistory.push(message)
        console.log(preHistory)
        this.$store.commit('chatModule/setGroupHistory', JSON.stringify(preHistory)); 
      } else if(this.chatTab === 'private') {
        message = {
          code: 1,
          sendUserId: this.memberId,
          receiverUserId: this.privateChatList[this.curPrivateIndex].memberId,
          sendTime: new Date().getTime(),
          message: this.chatValue,
          activityId: this.$store.state.activityId,
          avatar: this.memberInfo?this.memberInfo.avatar:''
        }
        if(this.curAvatarClickInfo.sendUserId) {
          message.receiverUserId = this.curAvatarClickInfo.sendUserId;
        } else {
          message.receiverUserId = this.privateChatList[this.curPrivateIndex].memberId
        }
        this.privateChatList[this.curPrivateIndex].chatRecords.push(message)
        this.$store.commit('chatModule/setPrivateList', JSON.stringify(this.privateChatList));
      }
      this.wsConnect?.send(JSON.stringify(message))
      this.chatValue = '';
    },
    // 聊天输入框聚焦
    chatInputFocus() {
      // 屏蔽发给云端的键盘消息
      this.larksr.op.setKeyboardEnable(false);
    },
    // 聊天输入框失焦
    chatInputBlur() {
      // 恢复发给云端的键盘消息
      this.larksr.op.setKeyboardEnable(true);
    },
    // 格式化时间
    timestampToTime(timestamp) {
      const date = new Date(+timestamp)
      const Y = date.getFullYear() + "-"
      const M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-"
      const D = date.getDate() < 10 ? "0" + date.getDate() + " " : date.getDate() + " "
      const h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ":"
      const m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ":"
      const s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      return Y + M + D + h + m + s
    },
    closeDialog() {
      this.$emit('closeDialog');
    },
    // 关闭名片弹窗
    closeCallingCardDialog() {
      this.showCallingCardDialog = false;
    },
    // 群聊某个头像点击
    groupPersonClick(row) {
      if(row.sendUserId) {
        if(row.sendUserId===this.memberInfo.sendUserId) return;
      }
      if(row.memberId) {
        if(row.memberId===this.memberInfo.sendUserId) return;
      }
      this.curAvatarClickInfo = row;
      this.showCallingCardDialog = true;
      this.getExchangeCard();
    },
    // 查询是否交换名片
    async getExchangeCard() {
      let params = {}
      if(this.chatTab === 'group') {
        params.cardMemberId = this.curAvatarClickInfo.sendUserId?this.curAvatarClickInfo.sendUserId:this.curAvatarClickInfo.memberId;
      } else if(this.chatTab === 'private') {
        params.cardMemberId = this.privateChatList[this.curPrivateIndex].memberId;
      }
      await getExchangeCard(params).then(res => {
        if(res && res.code === 1000) {
          this.exchangeCardInfo = res.result;
        }
      })
    },
    // 发起私聊
    wantPrivateChat() {
      const prePrivateList = this.$store.state.chatModule.privateList?JSON.parse(this.$store.state.chatModule.privateList): [];
      const index = prePrivateList.findIndex(item => item.memberId === (this.curAvatarClickInfo.sendUserId || this.curAvatarClickInfo.memberId));
      // 如果私聊列表没有 再push
      if(index === -1) {
        prePrivateList.unshift({
          avatar: this.curAvatarClickInfo.avatar,
          memberId: this.curAvatarClickInfo.sendUserId || this.curAvatarClickInfo.memberId,
          userTitle: this.curAvatarClickInfo.userTitle,
          userName: this.curAvatarClickInfo.userName,
          noReading: 0, // 消息未读数
          chatRecords: []
        })
        this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
        this.curPrivateIndex = 0;
      } else {
        this.curPrivateIndex = index;
        // 有消息未读，通知后台清空未读数，本地也将未读数设置为0
        if(prePrivateList[index].noReading>0) {
          prePrivateList[index].noReading = 0;
          this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
          const message = {
            code: 5,
            type: 21,
            receiverUserId: prePrivateList[index].memberId
          }
          this.wsConnect?.send(JSON.stringify(message));
        }
      }
      this.$store.commit('chatModule/setCurChatTab', 'private')
      this.$store.commit('chatModule/setCurPrivateMemberId', this.curAvatarClickInfo.sendUserId || this.curAvatarClickInfo.memberId)
      this.chatTab = 'private';
      this.$nextTick(() => {
        if(this.$refs.privateScroller) {
          let div = this.$refs.privateScroller.$refs["wrap"];
          this.$nextTick(()=> {
            div.scrollTop = div.scrollHeight;
          })
        }
      })
      this.curPrivateChat = this.curAvatarClickInfo;
      this.showCallingCardDialog = false;
      
      this.curAvatarClickInfo = {};
    },
    // 私聊头部交换名片图标点击
    privateTopExchangeCard() {
      this.showCallingCardDialog = true;
      this.getExchangeCard(); //查看是否交换名片
    },
    // 私聊交换名片取消
    privateEchangeCancle() {
      this.exchangeCardInfo = {};
      this.showCallingCardDialog = false;
    },
    // 私聊申请交换名片
    privateApplyChangeCard() {
      let message = {code:7,type:7,receiverUserId:this.privateChatList[this.curPrivateIndex].memberId};
      this.wsConnect.send(JSON.stringify(message));
      this.privateEchangeCancle();
    },
    // 申请交换名片
    applyChangeCard() {
      const receiverUserId = this.curAvatarClickInfo.sendUserId || this.curAvatarClickInfo.memberId;
      this.$emit('applyChangeCard', receiverUserId);
      this.wantPrivateChat();// 跳转到私聊tab
    },
    // 不同意交换名片
    disagreeExchangeCard(id, index) {
      let message = {
        code:7,
        type:13,
        receiverUserId: this.privateChatList[this.curPrivateIndex].memberId,
        ext: {
          msgId: id
        }
      }
      this.wsConnect.send(JSON.stringify(message));
      const prePrivateList = this.privateChatList;
      prePrivateList[this.curPrivateIndex].chatRecords[index].ext.agree = 0;
      this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
    },
    // 同意交换名片
    agreeExchangeCard(id, index) {
      let message = {
        code:7,
        type:10,
        receiverUserId: this.privateChatList[this.curPrivateIndex].memberId,
        ext: {
          msgId: id
        }
      }
      this.wsConnect.send(JSON.stringify(message));
      const prePrivateList = this.privateChatList;
      prePrivateList[this.curPrivateIndex].chatRecords[index].ext.agree = 1;
      this.$store.commit('chatModule/setPrivateList', JSON.stringify(prePrivateList));
    },
    // 聊天tab切换
    chatTabChange(val) {
      this.chatTab = val.name;
      this.$store.commit('chatModule/setCurChatTab', val.name)
      if(val.name==='private') {
        this.$nextTick(() => {
          if(this.$refs.privateHiddenScroller)this.$refs.privateHiddenScroller.scrollIntoView({behavior: "auto"});
        })
      } else if(val.name=== 'group') {
        this.$store.commit('chatModule/setGroupChatNoReading', false); // 清空群聊未读数
        this.$store.commit('chatModule/setCurPrivateMemberId', null)
        this.$nextTick(() => {
          if(this.$refs.hiddenScroller)this.$refs.hiddenScroller.scrollIntoView({behavior: "auto"});
        })
      }
      if(val.name==='private'&&this.privateChatList.length>0) {
        this.curPrivateIndex = 0;
        this.curPrivateChat = this.privateChatList[0];
        this.$store.commit('chatModule/setCurPrivateMemberId', this.privateChatList[0].memberId);
        // ************消息未读数归0
        const privateList = JSON.parse(this.$store.state.chatModule.privateList);
        // 有消息未读，通知后台清空未读数，本地也将未读数设置为0
        if(privateList[0].noReading>0) {
          privateList[0].noReading = 0;
          this.$store.commit('chatModule/setPrivateList', JSON.stringify(privateList));
          const message = {
            code: 5,
            type: 21,
            receiverUserId: privateList[0].memberId
          }
          this.wsConnect?.send(JSON.stringify(message));
        }
      }
    },
    // 点击一条私聊
    privateChatRowClick(row,index) {
      const privateList = JSON.parse(this.$store.state.chatModule.privateList);
      // 有消息未读，通知后台清空未读书，本地也将未读书设置为0
      if(privateList[index].noReading>0) {
        privateList[index].noReading = 0;
        this.$store.commit('chatModule/setPrivateList', JSON.stringify(privateList));
        const message = {
          code: 5,
          type: 21,
          receiverUserId: row.memberId
        }
        this.wsConnect?.send(JSON.stringify(message));
      }

      this.curPrivateIndex = index;
      this.curPrivateChat = row;
      this.$store.commit('chatModule/setCurPrivateMemberId', row.memberId);
      
    }
  },
  mounted() {}
}
</script>
