<template>
<div>
  <div v-if="dialogVisible" class="pxy-backpack-dialog">
    <div class="pxy-backpack-dialog-box">
      <el-row type="flex" justify="end">
        <i @click="closeDialog" class="el-icon-circle-close" style="font-size: 25px;cursor: pointer;"></i>
      </el-row>
      <div class="setting-tab">
        <el-tabs v-model="activeName">
          <el-tab-pane label="名片夹" name="callingCard"></el-tab-pane>
          <el-tab-pane label="奖品" name="gift"></el-tab-pane>
        </el-tabs>
      </div>
      <!-- 名片夹 -->
      <div class="calling-card" v-if="activeName === 'callingCard'">
        <el-button type="primary" round size="mini" :disabled="cardList.length===0" @click="cardDownloadFn">一键下载</el-button>
        <div v-if="cardList.length > 0">
          <div v-for="(item, index) in cardList" :key="index" class="card">
            <p>{{ item.company }}</p>
            <div>
              <span>{{ item.name }}</span>
              <span>{{ item.position }}</span>
            </div>
            <p>
              <span class="iconfont icon-dianhuatianchong"></span>
              <span>{{ item.phone }}</span>
            </p>
            <p>
              <span class="iconfont icon-weixin" style="font-size: 19px"></span>
              <span>{{ item.wechat }}</span>
            </p>
            <p>
              <span class="iconfont icon-youxiang" style="font-size: 17px"></span>
              <span>{{ item.email }}</span>
            </p>
          </div>
        </div>
        <div v-else class="table-empty">
            <el-image :src="require('@/assets/images/table_empty.png')"></el-image>
            <p>列表为空</p>
          </div>
      </div>
      <!-- 奖品 -->
      <div class="gift" v-if="activeName === 'gift'">
        <el-scrollbar style="max-height: 620px;">
          <table>
            <thead>
              <tr>
                <th>奖品等级</th>
                <th>奖品名称</th>
                <th>奖品图片</th>
                <th>中奖时间</th>
                <th>状态</th>
                <th>查看进度</th>
              </tr>
            </thead>
            <tbody>
              <template v-for="(item,index) in tableData">
                <tr>
                  <td>{{item.awardLevelValue}}</td>
                  <td>{{item.awardName}}</td>
                  <td>
                    <el-image  style="height: 70px" :src="item.awardPicture"  :preview-src-list="[item.url]"> </el-image>
                  </td>
                  <td>{{item.winningTimeSelf}}</td>
                  <td>
                    <el-button v-if="item.sendStatus===1" disabled type="info" round style="background:#656567;" size="mini">已领奖</el-button>
                    <el-button v-if="item.sendStatus===0" type="info" round size="mini" @click="winningInfoFn(item)">领奖</el-button>
                  </td>
                  <td><el-button type="info" round size="mini" @click="winningDetailOpen(item)">查看详情</el-button></td>
                </tr>
              </template>
            </tbody>
          </table>
        </el-scrollbar>
        <div v-if="tableData.length === 0" class="table-empty">
          <el-image :src="require('@/assets/images/table_empty.png')"></el-image>
          <p>列表为空</p>
        </div>
      </div>
    </div>
  </div>
  <!-- 发货弹窗 -->
  <div v-if="sendVisible" class="pxy-backpack-dialog">
    <div class="pxy-backpack-dialog-box form-self" style="width: 470px;min-height: 320px;">
      <el-row type="flex" justify="end">
        <i @click="closeSendDialog" class="el-icon-circle-close" style="font-size: 25px;cursor: pointer;"></i>
      </el-row>
      <div v-if="!isVirtual">
        <p style="text-align:center;margin-bottom: 20px">填写收货地址</p>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="收货人：" prop="receiver" style="color:#fff;" class="form-item">
            <el-input v-model="ruleForm.receiver" size="mini" class='input-self'></el-input>
          </el-form-item>
          <el-form-item label="手机号：" prop="receivingPhone" class="form-item">
            <el-input v-model="ruleForm.receivingPhone" size="mini" class='input-self'></el-input>
          </el-form-item>
          <el-form-item label="收货地址" prop="receivingAddress" class="form-item">
            <el-input v-model="ruleForm.receivingAddress" type="textarea" size="mini" class='input-self' ></el-input>
          </el-form-item>
        </el-form>
        <p style="font-size:12px;">为确保收到货品，请填写正确的手机号和收货地址，填写后不可修改</p>
        <el-row type="flex" justify="center" style="margin-top: 20px;">
          <el-button type="info" round size="mini" style="padding: 7px 50px;" @click="sendFn">确定</el-button>
        </el-row>
      </div>
      <div v-if="isVirtual">
        <p style="text-align:center;margin-bottom: 20px">平行云账号充值</p>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="收货人：" prop="receivingPhone" style="color:#fff;" class="form-item">
            <el-input v-model="ruleForm.receivingPhone" size="mini" class='input-self'></el-input>
          </el-form-item>
        </el-form>
        <p style="font-size:12px;margin-top: 30px;">请输入您在平行云的开发者账号（手机号），代金券稍后会充到您的账号中，账号内到余额可用于兑换时长。若是没有账号，会自动帮您注册一个账号。</p>
        <el-row type="flex" justify="center" style="margin-top: 65px;">
          <el-button type="info" round size="mini" style="padding: 7px 50px;" @click="sendFn">确定</el-button>
        </el-row>
      </div>
    </div>
  </div>
  <!-- 发货详情 -->
  <div v-if="sendDetailVisible" class="pxy-backpack-dialog">
    <div class="pxy-backpack-dialog-box form-self" style="width: 470px;min-height: 320px;">
      <el-row type="flex" justify="end">
        <i @click="closeSendDetailDialog" class="el-icon-circle-close" style="font-size: 25px;cursor: pointer;"></i>
      </el-row>
      <p style="text-align:center;margin-bottom: 20px">查看详情</p>
      <div class="send-detail">
        <template v-if="isVirtual">
          <p class="send-detail-phone">
            <span>充值手机号：</span>
            <span>{{ sendForm.receiverPhone }}</span>
          </p>
        </template>
        <template v-if="!isVirtual">
          <p class="send-detail-info">
            <span>收货人：</span>
            <span>{{ sendForm.receiver }}</span>
          </p>
          <p class="send-detail-info">
            <span>手机号：</span>
            <span>{{ sendForm.receiverPhone }}</span>
          </p>
          <p class="send-detail-info">
            <span>收货地址：</span>
            <span>{{ sendForm.receiverAddress }}</span>
          </p>
        </template>
        
      </div>
      <p class="send-detail-desc">
        <span>查询进度：</span>
        <span>奖品发货后会发短信通知您，请注意查收</span>
      </p>
      <p style="font-size: 12px;margin: 5px 0 20px 85px;">请联系主办方查询进度，联系方式，010-8838338</p>
    </div>
  </div>
</div>
  
</template>
<script>
import { getCard, cardDownload, getWinningList, apiPostWinningSend, getWinningDetail } from '@/apis/api'
import { timestampToTime } from '@/utils/tools'

const validatePhone = (rule, value, callback) => {
  if (!value) {
    callback(new Error('请输入手机号！'))
  } else if (!/^1[3456789]\d{9}$/.test(value)) {
    callback(new Error('请输入正确的手机号'))
  } else {
    callback()
  }
}
export default {
  props: {
    'dialogVisible': Boolean,
    'closeIcon': {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      activeName: 'callingCard', // 当前tab页
      tableData: [],// 奖品表格
      ruleForm: {
        receiver: '',
        receivingPhone: '',
        receivingAddress: '',
      },
      sendForm: { // 发货详情
        receiver: '',
        receivingPhone: '',
        receivingAddress: '',
      },
      rules: {
        receiver: [ { required: true, message: '请输入收货人', trigger: 'blur' }, ],
        receivingPhone: [ { required: true, message: '请输入手机号', trigger: 'blur' }, { validator: validatePhone, trigger: 'blur' }],
        receivingAddress: [ { required: true, message: '请输入收货地址', trigger: 'blur' }, ],
      },
      cardList: [], // 名片夹
      sendVisible: false, // 发货弹窗
      sendDetailVisible: false, // 发货详情弹窗
      activityId: '',
      tableHandleRow: {},
      isVirtual: '', // 是否是虚拟奖品
    }
  },
  watch: {
    'dialogVisible': {
      handler(newValue) {
        console.log(newValue)
        if(newValue) {
          this.getCallingCardList();
          this.getWinningList();
        }
      }
    }
  },
  methods: {
    closeDialog() {
      this.$emit('closeDialog')
    },
    closeSendDialog() {
      this.sendVisible = false;
    },
    closeSendDetailDialog() {
      this.sendDetailVisible = false;
    },
    // 获取名片夹列表
    getCallingCardList() {
      const params = {
        activityId : this.$store.state.activityId,
        page: 1,
        pageSize: 5000
      }
      getCard(params).then(res => {
        if(res && res.code === 1000) {
          console.log(res)
          this.cardList = res.result.records;
        }
      })
    },
    // 名片夹一键下载
    cardDownloadFn() {
      const params = {
        activityId : this.$store.state.activityId,
      }
      cardDownload(params).then((res) => {
        if (res) {
          const link = document.createElement('a')
          const blob = new Blob([res], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' })
          link.style.display = 'none'
          link.href = URL.createObjectURL(blob)

          link.download = '名片夹' // 下载的文件名
          document.body.appendChild(link)
          link.click()
          document.body.removeChild(link)
        }
      })
    },
    // 中奖列表查询
    getWinningList() {
      const params = {
        activityId : this.$store.state.activityId,
        memberId: this.$store.state.memberId,
        page: 1,
        pageSize: 5000
      }
      getWinningList(params).then(res => {
        if(res && res.code === 1000) {
          this.tableData = res.result.records.map(item => {
            return Object.assign(item, {
              winningTimeSelf: timestampToTime(item.winningTime)
            })
          })
        }
      })
    },
    // 领奖
    winningInfoFn(row) {
      this.tableHandleRow = row;
      if(row.awardTypeValue === '虚拟') this.isVirtual = true;
      if(row.awardTypeValue === '实物') this.isVirtual = false;
      this.sendVisible = true;
    },
    // 确定
    sendFn() {
      this.$refs.ruleForm.validate((valid) => {
      if (valid) {
        let params = {
          id: this.tableHandleRow.id || '',
        }
        if (this.tableHandleRow.awardType === 1) {
          params = Object.assign(this.ruleForm, params)
        } else if (this.tableHandleRow.awardType === 2) {
          params = Object.assign({ receivingPhone: this.ruleForm.receivingPhone }, params)
        }
        apiPostWinningSend(params).then((res) => {
          if (res) {
            this.$message.success('发货成功')
            this.$refs.ruleForm.resetFields()
            this.closeSendDialog()
          }
        }) 
      } else {
          console.log('error submit!', fields)
      }
      })
    },
    // 打开详情弹窗
    winningDetailOpen(row) {
      this.winningDetail();
      this.tableHandleRow = row;
      if(row.awardTypeValue === '虚拟') this.isVirtual = true;
      if(row.awardTypeValue === '实物') this.isVirtual = false;
      this.sendDetailVisible = true;
    },
    // 查看详情
    winningDetail() {
      let params = {
        id: this.tableHandleRow.id || '',
      }
      getWinningDetail(params).then(res => {
        if(res && res.code === 1000) {
          const data = res.result
          if (data) {
            // sendForm.expressCompany = data.expressCompany || ''
            // sendForm.expressNumber = data.expressNumber || ''
            sendForm.receiver = data.receiver || ''
            sendForm.receivingAddress = data.receivingAddress || ''
            sendForm.receivingPhone = data.receivingPhone || ''
          }
        }
      })
    }
  },
  created() {
    this.activityId = this.$store.state.activityId;
  },
  mounted() {},
}
</script>
<style lang='scss'>
 .pxy-backpack-dialog {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   color: #fff;
   z-index: 2;
   &-box {
     width: 906px;
     min-height: 500px;
     padding: 20px 30px;
     border-radius: 8px;
     background-color: #2e2e2e99;
     .calling-card {
       >div:nth-of-type(1) {
         margin-top: 20px;
       }
       .card {
        width: 259px;
        height: 162px;
        border-radius: 8px;
        border: 2px solid #cccccc66;
        box-sizing: border-box;
        background: #2e2e2e4d;
        box-shadow: 2px 4px 4px #0000000d;
        font-size: 14px;
        padding: 15px 25px;
        display: inline-block;
        margin: 5px;
        >p:nth-of-type(1) {
          margin-bottom: 15px;
        }
        >p:nth-of-type(2),>p:nth-of-type(3),>p:nth-of-type(4) {
          line-height: 22px;
          >span:nth-of-type(1) {
            margin-right: 25px;
          }
        }
        >div:nth-of-type(1) {
          border-bottom: 1px solid #EBEAED;
          padding-bottom: 5px;
          margin-bottom: 5px;
          >span {
            margin-right: 10px;
            font-weight: 600;
          }
        }
       }
     }
     .gift {
       table {
         width: 100%;
         border-collapse: collapse;
         font-size: 14px;
         thead {
           tr {
             height: 50px;
             padding: 10px 0;
            //  border-bottom: 1px solid #cad3e4;
             border-bottom: 1px solid #cccccc66;
             text-align: center;
             >th {
               line-height: 50px;
             }
             >th:nth-of-type(1) {
               width: 16%;
             }
             >th:nth-of-type(2) {
               width: 17%;
             }
             >th:nth-of-type(3) {
               width: 14%;
             }
             >th:nth-of-type(4) {
               width: 17%;
             }
             >th:nth-of-type(5) {
               width: 17%;
             }
             >th:nth-of-type(6) {
               width: 17%;
             }
           }
         }
         tbody {
           tr {
            //  border-bottom: 1px solid #9ca6b7;
             border-bottom: 1px solid #cccccc66;
             >td {
               padding: 10px 0;
               text-align: center;
               vertical-align: middle;
             }
             >td:nth-of-type(1) {
               width: 16%;
             }
             >td:nth-of-type(2) {
               width: 17%;
             }
             >td:nth-of-type(3) {
               width: 14%;
             }
             >td:nth-of-type(4) {
               width: 17%;
             }
             >td:nth-of-type(5) {
               width: 17%;
             }
             >td:nth-of-type(6) {
               width: 17%;
             }
           }
         }
       }
       }
     }
  //  }
  .form-self {
    .demo-ruleForm {
      .form-item .el-form-item__label {
        color: #fff!important;
      }
    }
    
  }
  .el-tabs__item {
    color: #fff!important;
  }
  .el-tabs__item.is-active {
    color: #32cdcd!important;
  }
  .input-self input,.input-self textarea {
    background-color: #2e2e2e99!important;
    color: #fff!important;
  }
  .input-self input:focus {
    border: 1px solid #fff!important;
  }
  .input-self textarea:focus {
    border: 1px solid #fff!important;
  }
  .send-detail {
    margin: 0 15px;
    border: 2px solid #9d9d9f;
    border-radius: 10px;
    padding: 20px;
    font-size: 14px;
    &-phone,&-info {
      margin-bottom: 60px;
      >span:nth-of-type(2) {
        font-size: 12px;
      }
    }
    &-info {
      margin-bottom: 10px;
    }
  }
  .send-detail-desc {
    font-size: 12px;
    margin: 0 20px;
    margin-top: 20px;
    >span:nth-of-type(1) {
      display: inline-block;
      width: 65px;
    }
  }
 }
</style>