<template>
  <div>
     <div class="admin-bar">
       <div @click.prevent="iconClick('person')">
        <el-badge :hidden="!memberHandupTotalNum" :value="memberHandupTotalNum" class="item">
          <el-tooltip class="item" effect="dark" placement="left" content="人员列表">
            <span :class="{'iconfont':true, 'icon-qunliao': true, 'is-current': curIcon==='person'}"></span>
          </el-tooltip>
        </el-badge>
       </div>
       <div @click.prevent="iconClick('video')">
        <el-tooltip class="item" effect="dark" placement="left" content="左侧小屏镜头切换">
          <span :class="{'iconfont':true, 'icon-video': true, 'is-current': curIcon==='video'}"></span>
        </el-tooltip>
       </div>
       <div @click.prevent="iconClick('permission')">
        <el-tooltip class="item" effect="dark" placement="left" content="会场控制">
          <span :class="{'iconfont':true, 'icon-quanxian': true, 'is-current': curIcon==='permission'}"></span>
        </el-tooltip>
       </div>
       <div @click.prevent="iconClick('broadcast')">
        <el-tooltip class="item" effect="dark" placement="left" content="全域广播">
          <span :class="{'iconfont':true, 'icon-zizhutuiguang': true, 'is-current': curIcon==='broadcast'}"></span>
        </el-tooltip>
       </div>
       <div @click.prevent="iconClick('music')">
        <el-tooltip class="item" effect="dark" placement="left" content="背景音乐管理">
          <span :class="{'iconfont':true, 'icon-yinle': true, 'is-current': curIcon==='music'}"></span>
        </el-tooltip>
       </div>
       <div @click.prevent="iconClick('light')">
        <el-tooltip class="item" effect="dark" placement="left" content="环境设置">
          <span :class="{'iconfont':true, 'icon-huanjingguang': true, 'is-current': curIcon==='light'}"></span>
        </el-tooltip>
       </div>
       <div @click.prevent="iconClick('gift')">
        <el-tooltip class="item" effect="dark" placement="left" content="抽奖管理">
          <span :class="{'iconfont':true, 'icon-choujiang': true, 'is-current': curIcon==='gift'}"></span>
        </el-tooltip>
       </div>
     </div>
     <!-- 人员列表 -->
     <div ref="memberdiv" v-if="curIcon==='person'" class="dialog-box" style="padding: 20px 0;">
       <p style="margin-bottom: 20px;padding: 0 20px;" class="font-size-14">人员列表（{{memberTotalNum}}）</p>
       <div style="padding: 0 20px;" :class="{'marginB-20':searchValue!==''}">
         <el-input 
           class="input-self" 
           placeholder="搜索" 
           prefix-icon="el-icon-search" 
           v-model="searchValue" 
           size="mini"
            @blur="searchMemberList" 
            @keyup.enter.native="searchMemberList"
            @clear="searchMemberListCancle"
           clearable
          ></el-input>
       </div>
       <el-tabs v-if="searchValue===''" v-model="memberCurTab" style="margin-top: 10px;padding: 0 20px;" @tab-click="memberTabChange">
        <el-tab-pane name="speaker">
          <span slot="label">
            <el-badge :hidden="!speakerDotNum" :value="speakerDotNum" class="person-list-badge item">
              演讲嘉宾
            </el-badge>
          </span>
        </el-tab-pane>
        <el-tab-pane name="audience">
          <span slot="label">
            <el-badge :hidden="!audienceDotNum" :value="audienceDotNum" class="person-list-badge item">
              观众
            </el-badge>
          </span>
        </el-tab-pane>
      </el-tabs>
      <el-scrollbar style="height: calc(100% - 120px)">
        <div style="padding: 0 15px;">
          <template v-if="personList.length > 0">
            <template v-for="(item,index) in personList">
              <el-row type="flex" class="person-card" justify="space-between;">
                <el-col :span="5">
                  <div style="position: relative;padding-top:8px;max-height:55px;">
                    <el-image style="width:38px;height: 38px;border-radius:50%;" :src="item.avatar"></el-image>
                    <div v-if="item.handUp===1" class="jushou-font"><span class="iconfont icon-shou"></span></div>
                  </div>
                  <el-image v-if="item.userTitle==='演讲嘉宾'" style="width:60px;" :src="require('@/assets/images/speaker_tag.png')"></el-image>
                </el-col>
                <el-col :span="8">
                  <p>
                    <span class="hidden-span" :title="item.company">{{ item.company }}</span> 
                    <br/>
                    <span :title="item.position" class="hidden-span">{{ item.position }}</span>
                  </p>
                  <p><span class="hidden-span" :title="item.userName">{{ item.userName }}</span></p>
                </el-col>
                <el-col :span="11">
                  <el-row type="flex" justify="space-around" style="height:100%;align-items: center;">
                    <template>
                      <el-tooltip v-if="item.stage===1" class="item" effect="dark" content="允许上台" placement="top">
                        <el-image style="width:22px;height:22px" @click="stageIconClick(0,item.memberId)" :src="require('@/assets/images/stage.png')"></el-image>
                      </el-tooltip>
                      <el-tooltip v-if="item.stage===0" class="item" effect="dark" content="禁止上台" placement="top">
                        <el-image style="width:22px;height:22px" @click="stageIconClick(1,item.memberId)" :src="require('@/assets/images/stage_ban.png')"></el-image>
                      </el-tooltip>
                    </template>
                    <template>
                      <el-tooltip v-if="item.screen===1" class="item" effect="dark" content="允许投屏" placement="top">
                        <el-image style="width:22px;height:18px" @click="screenIconClick(0, item.memberId)" :src="require('@/assets/images/projection.png')"></el-image>
                      </el-tooltip>
                      <el-tooltip v-if="item.screen===0" class="item" effect="dark" content="禁止投屏" placement="top">
                        <el-image style="width:22px;height:22px" @click="screenIconClick(1, item.memberId)" :src="require('@/assets/images/projection_ban.png')"></el-image>
                      </el-tooltip>
                      <el-tooltip v-if="item.screen===2" class="item" effect="dark" content="正在投屏" placement="top">
                        <el-image style="width:22px;height:22px" @click="screenIconClick(0, item.memberId)" :src="require('@/assets/images/projection_ing.png')"></el-image>
                      </el-tooltip>
                    </template>
                    <template>
                      <el-tooltip v-if="item.voice===1" class="item" effect="dark" content="允许发言" placement="top">
                        <el-image style="width:22px;height:22px" @click="voiceIconClick(0, item.memberId)" :src="require('@/assets/images/mic.png')"></el-image>
                      </el-tooltip>
                      <el-tooltip v-if="item.voice===0" class="item" effect="dark" content="禁止发言" placement="top">
                        <el-image style="width:22px;height:22px" @click="voiceIconClick(1, item.memberId)" :src="require('@/assets/images/mic_ban.png')"></el-image>
                      </el-tooltip>
                      <el-tooltip v-if="item.voice===2" class="item" effect="dark" content="正在发言" placement="top">
                        <el-image style="width:22px;height:22px" :src="require('@/assets/images/mic_ing.png')"></el-image>
                      </el-tooltip>
                    </template>
                    <template>
                      <el-tooltip v-if="item.lighting===0" class="item" effect="dark" content="灯光聚焦" placement="top">
                        <el-image style="width:22px;height:22px" @click="lightIconClick(1, item.memberId)" :src="require('@/assets/images/light.png')"></el-image>
                      </el-tooltip>
                      <el-tooltip v-if="item.lighting===1" class="item" effect="dark" content="关闭灯光聚焦" placement="top">
                        <el-image style="width:22px;height:22px" @click="lightIconClick(0, item.memberId)" :src="require('@/assets/images/light_ban.png')"></el-image>
                      </el-tooltip>
                    </template>
                    <template>
                      <el-tooltip class="item" effect="dark" content="踢除用户" placement="top">
                        <el-image @click="kickOut(item)" style="width:22px;height:22px" :src="require('@/assets/images/person_del.png')"></el-image>
                      </el-tooltip>
                    </template>
                  </el-row>
                </el-col>
                
              </el-row>
            </template>
          </template>
          <template v-else>
            <div class="table-empty">
              <el-image :src="require('@/assets/images/table_empty.png')"></el-image>
              <p>列表为空</p>
            </div>
          </template>
        </div>
      </el-scrollbar>
     </div>
     <!-- 左侧小屏镜头切换 -->
     <div v-if="curIcon==='video'" class="dialog-box">
      <p style="margin-bottom: 20px;"> <span class="font-size-14">左侧小屏镜头切换</span>
        <el-tooltip class="item" effect="dark" placement="bottom-start">
          <div slot="content">镜头切换：可切换会场中左侧小屏<br />的镜头，分别为从观众席向讲台方<br />向的讲台镜头，从顶部朝下的顶视<br />镜头和从主讲台到观众席的观众镜<br />头</div>
          <span class="iconfont icon-31yiwen"></span>
        </el-tooltip>
      </p>
      <el-row type="flex" class="video-row">
        <span :class="{'iconfont':true, 'icon-video': true, 'is-current': curIcon==='video'}"></span>
        <span>讲台镜头</span>
        <el-switch @change="shotChange" :value="shotSwitchValue" :active-value="1" :inactive-value="0"  active-color="#fff" inactive-color="#fff"></el-switch>
      </el-row>
      <el-row type="flex" class="video-row">
        <span :class="{'iconfont':true, 'icon-video': true, 'is-current': curIcon==='video'}"></span>
        <span>顶视镜头</span>
        <el-switch @change="shotChange" :value="shotSwitchValue" :active-value="2" :inactive-value="0"  active-color="#fff" inactive-color="#fff"></el-switch>
      </el-row>
      <el-row type="flex" class="video-row">
        <span :class="{'iconfont':true, 'icon-video': true, 'is-current': curIcon==='video'}"></span>
        <span>观众镜头</span>
        <el-switch @change="shotChange" :value="shotSwitchValue" :active-value="3" :inactive-value="0"  active-color="#fff" inactive-color="#fff"></el-switch>
      </el-row>
     </div>
     <!-- 会场控制 -->
     <div v-if="curIcon==='permission'" class="dialog-box">
       <p style="margin-bottom: 20px;" class="font-size-14">会场控制</p>
       <el-row type="flex" class="permission-row">
          <span>一键就座</span>
          <el-tooltip class="item" effect="dark" placement="bottom-start">
            <div slot="content">在会场内的观众（不包括讲台上人），管<br />理员可控制一键就座位，站立的或未找到<br />座位的观众按照一定规则分配位置，瞬间<br />移动到座位上，就座后还可移动。</div>
            <span class="iconfont icon-31yiwen"></span>
          </el-tooltip>
          <el-button type="primary" size="mini" round style="padding: 5px 20px;" @click="allSitDownClick">一键就座</el-button>
          <!-- <el-switch :value="switchValue"  active-color="#fff" inactive-color="#fff"></el-switch> -->
        </el-row>
        <el-row type="flex" class="permission-row">
          <span>全员静音</span>
          <el-tooltip class="item" effect="dark" placement="bottom-start">
            <div slot="content">会场内的除管理员之外的所有人员（包括 <br />嘉宾、记者、观众），麦克风设置为不可<br />用状态</div>
            <span class="iconfont icon-31yiwen"></span>
          </el-tooltip>
          <el-switch @change="allMuteClick" :value="muteSwitchValue" :active-value="1" :inactive-value="0"  active-color="#fff" inactive-color="#fff"></el-switch>
        </el-row>
        <el-row type="flex" class="permission-row">
          <span>烟花开关</span>
          <el-tooltip class="item" effect="dark" placement="bottom-start">
            <div slot="content">可在会议开始或结束时，放烟花特效，可<br />控制开关，打开时循环播放烟花特效，关<br />闭后停止播放。</div>
            <span class="iconfont icon-31yiwen"></span>
          </el-tooltip>
          <el-switch @change="fireworksClick" :value="fireworksSwitchValue" :active-value="1" :inactive-value="0" active-color="#fff" inactive-color="#fff"></el-switch>
        </el-row>
     </div>
     <!-- 全域广播 -->
     <div v-if="curIcon==='broadcast'" class="dialog-box">
      <p style="margin-bottom: 20px;" class="font-size-14">全域广播</p>
      <div class="broadcast-row">
        <p class="broadcast-desc">长按可进行全域广播，抬起则关闭广播开关。全场景登陆的用户都可以听到广播，不受距离限制，例如会议快开始时，可喊话所有人尽快到会场，类似于广播站。</p>
        <div>
          <!-- @mouseleave="stopRecode" -->
          <div
            :class="{'noselect': true, 'broadcast-btn': true, 'broadcast-recoding-btn': isRecode}"
            @mousedown="startRecode"
            @mouseup="stopRecode"
          >
            <span class="iconfont icon-user-talk"></span>
            <p>长按说话</p>
          </div>
          <p class="broadcast-btn-desc">长按可进行全域广播，抬起则关闭广播开关</p>
        </div>
      </div>
     </div>
     <!-- 背景音乐管理 -->
     <div v-if="curIcon==='music'" class="dialog-box">
      <p style="margin-bottom: 20px;">
        <span class="font-size-14">背景音乐管理</span>
        <el-tooltip class="item" effect="dark" placement="bottom-start">
          <div slot="content">全场景的背景音管理，可开关背景音<br />，可选择播放的歌曲，并可调节背景<br />音的大小。默认单曲循环，打开列表<br />循环后，播放完当前歌曲后按列表循<br />环播放。</div>
          <span class="iconfont icon-31yiwen"></span>
        </el-tooltip>
      </p>
      <el-row type="flex" class="music-row">
        <span>背景音开关</span>
        <el-switch @change="bgmClick" :value="bgmSwitchValue" :active-value="1" :inactive-value="0" active-color="#fff" inactive-color="#fff"></el-switch>
      </el-row>
      <el-row type="flex" class="music-row" style="margin-bottom: 10px;">
        <span>列表循环播放</span>
        <el-switch @change="bgmCirculClick" :value="bgmCirculSwitchValue" active-value="circulate " inactive-value="single " active-color="#fff" inactive-color="#fff"></el-switch>
      </el-row>
      <el-row type="flex" class="music-row" style="margin-bottom: 10px;">
        <span style="line-height:38px;">背景音声音大小</span>
        <el-slider style="width: calc(100% - 105px);" v-model="bgmVolume" @change="chagneBgmVolume"></el-slider>
      </el-row>
      <el-row type="flex" class="music-row" style="margin-bottom: 0;">
        <span>背景音乐列表</span>
      </el-row>
      <div>
        <table>
          <thead>
            <tr>
              <th>音乐名</th>
              <th style="text-align:center;">时长</th>
              <th style="text-align:center;">播放/暂停</th>
            </tr>
          </thead>
        </table>
        <div v-if="bgmList.length>0" class="bgm-tbody" style="width: 100%;">
          <el-scrollbar style="height: 220px;width: 100%;">
            <template v-for="(item,index) in bgmList">
              <div class="bgm-tbody-tr">
                <div>{{item.name}}</div>
                <div style="text-align:center;">{{item.mediaContent}}</div>
                <div style="text-align:center;">
                  <i @click="bgmPauseClick(index)" v-if="item.isPlay" class="el-icon-video-pause"></i>
                  <i @click="bgmPlayClick(index)" v-else class="el-icon-video-play"></i>
                </div>
              </div>
            </template>
          </el-scrollbar>
        </div>
        <div v-else class="table-empty">
          <el-image :src="require('@/assets/images/table_empty.png')"></el-image>
          <p>列表为空</p>
        </div>
      </div>
     </div>
     <!-- 环境设置 -->
     <div v-if="curIcon==='light'" class="dialog-box">
       <p style="margin-bottom: 20px;">
          <span class="font-size-14">环境设置</span>
          <el-tooltip class="item" effect="dark" placement="bottom-start">
            <div slot="content">可从预制的场景中选择环境，如晴天或雨<br />天，可设置是否按当前时间更换环境</div>
            <span class="iconfont icon-31yiwen"></span>
          </el-tooltip>
        </p>
        <el-row type="flex" class="permission-row" style="margin-bottom: 0;">
          <span style="font-weight:600;">天气设置</span>
        </el-row>
        <el-row type="flex">
          <div class="environment">
            <el-image style="width:77px;height:77px" :src="require('@/assets/images/weather_sun.png')"></el-image>
            <i class="el-icon-success"></i>
          </div>
          <div class="environment" style="cursor:not-allowed">
            <el-image style="width:77px;height:77px" :src="require('@/assets/images/weather_autumn.png')"></el-image>
            <!-- <i class="el-icon-success"></i> -->
          </div>
          <div class="environment" style="cursor:not-allowed">
            <el-image style="width:77px;height:77px" :src="require('@/assets/images/weather_winter.png')"></el-image>
            <!-- <i class="el-icon-success"></i> -->
          </div>
        </el-row>
        <el-row type="flex" class="permission-row" style="margin-bottom: 20px;">
          <span style="font-weight:600;">时间设置</span>
        </el-row>
        <el-row type="flex" class="permission-row">
          <span style="width: 125px;">按当前时间</span>
          <el-switch @change="curTimeChange" :value="curTimeSwitchValue" :active-value="1" :inactive-value="0" active-color="#fff" inactive-color="#fff"></el-switch>
        </el-row>
        <el-row type="flex" class="permission-row">
          <span style="width: 125px;">按固定时间点</span>
          <el-switch @change="curTimeChange" :value="curTimeSwitchValue" :active-value="0" :inactive-value="1"  active-color="#fff" inactive-color="#fff"></el-switch>
        </el-row>
        <div class="time-choose">
          <div>
            <div class="time-choose-sun"></div>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('6:00','left')">
            <el-image v-show="fixedTime==='6:00'&&leftTime6==='left'" style="width:20px;height:20px" :src="require('@/assets/images/light6_cur.png')"></el-image>
            <el-image v-show="leftTime6!=='left'" style="width:20px;height:20px" :src="require('@/assets/images/light6.png')"></el-image>
            <p>6:00</p>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('9:00')">
           
            <el-image v-show="fixedTime==='9:00'" style="width:20px;height:20px" :src="require('@/assets/images/light9_cur.png')"></el-image>
            <el-image v-show="fixedTime!=='9:00'" style="width:20px;height:20px" :src="require('@/assets/images/light9.png')"></el-image>
            <p>9:00</p>
            <div v-show="curTimeSwitchValue===0&&fixedTime==='9:00'" class="line-time">
              <div style="height: 27px;"></div>
              <p>9:00</p>
            </div>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('12:00')">
            <el-image v-show="fixedTime==='12:00'" style="width:20px;height:20px" :src="require('@/assets/images/light12_cur.png')"></el-image>
            <el-image v-show="fixedTime!=='12:00'" style="width:20px;height:20px" :src="require('@/assets/images/light12.png')"></el-image>
            <p>12:00</p>
            <div v-show="curTimeSwitchValue===0&&fixedTime==='12:00'" class="line-time">
              <div style="height: 49px;"></div>
              <p>12:00</p>
            </div>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('15:00')">
            <el-image v-show="fixedTime==='15:00'" style="width:20px;height:20px" :src="require('@/assets/images/light15_cur.png')"></el-image>
            <el-image v-show="fixedTime!=='15:00'" style="width:20px;height:20px" :src="require('@/assets/images/light15.png')"></el-image>
            <p>15:00</p>
            <div v-show="curTimeSwitchValue===0&&fixedTime==='15:00'" class="line-time">
              <div style="height: 64px;"></div>
              <p>15:00</p>
            </div>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('18:00')">
            <el-image v-show="fixedTime==='18:00'" style="width:20px;height:20px" :src="require('@/assets/images/light18_cur.png')"></el-image>
            <el-image v-show="fixedTime!=='18:00'" style="width:20px;height:20px" :src="require('@/assets/images/light18.png')"></el-image>
            <p>18:00</p>
            <div v-show="curTimeSwitchValue===0&&fixedTime==='18:00'" class="line-time">
              <div style="height: 67px;"></div>
              <p>18:00</p>
            </div>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('21:00')">
            <el-image v-show="fixedTime==='21:00'" style="width:20px;height:20px" :src="require('@/assets/images/light21_cur.png')"></el-image>
            <el-image v-show="fixedTime!=='21:00'" style="width:20px;height:20px" :src="require('@/assets/images/light21.png')"></el-image>
            <p>21:00</p>
            <div v-show="curTimeSwitchValue===0&&fixedTime==='21:00'" class="line-time">
              <div style="height: 64px;"></div>
              <p>21:00</p>
            </div>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('0:00')">
            <el-image v-show="fixedTime==='0:00'" style="width:20px;height:20px" :src="require('@/assets/images/light0_cur.png')"></el-image>
            <el-image v-show="fixedTime!=='0:00'" style="width:20px;height:20px" :src="require('@/assets/images/light0.png')"></el-image>
            <p>0:00</p>
            <div v-show="curTimeSwitchValue===0&&fixedTime==='0:00'" class="line-time">
              <div style="height: 49px;"></div>
              <p>0:00</p>
            </div>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('3:00')">
            <el-image v-show="fixedTime==='3:00'" style="width:20px;height:20px" :src="require('@/assets/images/light3_cur.png')"></el-image>
            <el-image v-show="fixedTime!=='3:00'" style="width:20px;height:20px" :src="require('@/assets/images/light3.png')"></el-image>
            <p>3:00</p>
            <div v-show="curTimeSwitchValue===0&&fixedTime==='3:00'" class="line-time">
              <div style="height: 27px;"></div>
              <p>3:00</p>
            </div>
          </div>
          <div class="time-choose-time" @click.prevent="changeFixedTime('6:00','right')">
            <el-image v-show="fixedTime==='6:00'&&leftTime6==='right'" style="width:20px;height:20px" :src="require('@/assets/images/light6_cur.png')"></el-image>
            <el-image v-show="leftTime6!=='right'" style="width:20px;height:20px" :src="require('@/assets/images/light6.png')"></el-image>
            <p>6:00</p>
          </div>
        </div>
     </div>
     <!-- 抽奖管理 -->
     <div v-if="curIcon==='gift'" class="dialog-box">
       <p style="margin-bottom: 20px;" class="font-size-14">抽奖管理</p>
       <el-row type="flex" class="permission-row">
          <span>寻宝开关</span>
          <el-tooltip class="item" effect="dark" placement="bottom-start">
            <div slot="content">可显示或隐藏宝箱，关闭后场景中的隐藏<br />宝箱，可在寻宝环节打开寻宝开关</div>
            <span class="iconfont icon-31yiwen"></span>
          </el-tooltip>
          <el-switch @change="giftSwitchChange" :value="giftSwitchValue" :active-value="1" :inactive-value="0" active-color="#fff" inactive-color="#fff"></el-switch>
        </el-row>
        <el-row type="flex" class="permission-row">
          <span>抽奖开关</span>
          <el-tooltip class="item" effect="dark" placement="bottom-start">
            <div slot="content">抽奖开关打开后，会场内的大屏切到抽奖<br />页面，可抽奖</div>
            <span class="iconfont icon-31yiwen"></span>
          </el-tooltip>
          <el-switch @change="luckySwitchChange" :value="luckySwitchValue" :active-value="1" :inactive-value="0" active-color="#fff" inactive-color="#fff"></el-switch>
        </el-row>
        <el-row type="flex" class="permission-row" style="margin-bottom: 0;">
          <span>抽奖设置</span>
        </el-row>
        <div class="gift-table">
          <table>
            <thead>
              <tr>
                <th>奖品</th>
                <th>总数量</th>
                <th>剩余数量</th>
                <th>本轮抽奖数量</th>
                <th>开始抽奖</th>
              </tr>
            </thead>
          </table>
          <div v-if="tableData.length>0" class="gift-tbody" style="width: 100%;">
            <el-scrollbar style="height: 260px;width: 100%;">
              <template v-for="(item,index) in tableData">
                <div class="gift-tbody-tr">
                  <div>
                    <p style="line-height:20px;">{{item.awardLevelValue}}<br/>{{item.awardName}}</p>
                  </div>
                  <div>{{item.leftoverNum}}</div>
                  <div>
                    <p>{{item.totalNum}}</p>
                  </div>
                  <div>
                    <i class="el-icon-caret-left" style="font-size: 16px;cursor: pointer;" @click="numberSub(item,index)"></i>
                    <input v-model="item.number" class="gift-input" @blur="numberChange(item,index)" />
                    <i class="el-icon-caret-right" style="font-size: 16px;cursor: pointer;" @click="numberAdd(item,index)"></i>
                  </div>
                  <div>
                    <el-button type="primary" style="padding: 7px 10px;" size="mini" round :disabled="item.number<1" @click="luckyDraw(item)">抽奖</el-button>
                  </div>
                </div>
              </template>
            </el-scrollbar>
          </div>
          <div v-else class="table-empty">
            <el-image :src="require('@/assets/images/table_empty.png')"></el-image>
            <p>列表为空</p>
          </div>
        </div>
     </div>
  </div>
</template>
<script>
import { getBgmList, getAwardList, luckyDraw, getMemberList, apiAllowStage, apiAllowScreen, apiAllowVoice } from '@/apis/api'
export default {
  data() {
    return {
      // larksr: this.$store.state.larksr,
      activityId: this.$store.state.activityId,
      curIcon: '',
      showVideo: true, // 切换镜头弹窗
      shotSwitchValue: 0, //左侧小屏镜头切换
      muteSwitchValue: 0, // 全员静音
      fireworksSwitchValue: 0, // 烟花开关
      bgmSwitchValue: 0, // 背景音乐开关
      bgmCirculSwitchValue: 'single', // 循环播放
      bgmList: [], // 需要播放的音乐列表
      curTimeSwitchValue: 0, // 环境设置-按照当前时间
      fixedTime: '6:00',
      leftTime6: 'left',
      luckySwitchValue: 0, // 抽奖开关
      giftSwitchValue: 0, // 寻宝开关
      switchValue: true,
      bgmVolume: 40,
      tableData: [],
      searchValue: '',
      memberCurTab: 'speaker',
      personList: [],
      speakerList: [],
      audienceList: [],
      isRecode: false,// 开始说话
      audienceDotNum: 0, // 观众举手的小红点标识
      speakerDotNum: 0, // 演讲嘉宾的小红点标识
    }
  },
  watch: {
    '$store.state.chatModule.refreshMemberList': {
      handler(newVal) {
        if(newVal) {
          this.memberList(1);
          this.memberList(3);
          this.$store.commit('chatModule/setRefreshMemberList', false)
        }
      },
    },
    '$store.state.chatModule.adminPanel': {
      handler(newVal) {
        if(newVal) {
          const adminPanelObj = JSON.parse(newVal)
          if(adminPanelObj.mainCamera===1) this.shotSwitchValue = 1;
          if(adminPanelObj.leftCamera===1) this.shotSwitchValue = 2;
          if(adminPanelObj.rightCamera===1) this.shotSwitchValue = 3;
          if(adminPanelObj.currentTime===1) this.curTimeSwitchValue = 1;
          if(adminPanelObj.fixedTime===1) this.curTimeSwitchValue = 0;
          this.muteSwitchValue = adminPanelObj.allMute;
          this.fireworksSwitchValue = adminPanelObj.fireworks;
          this.bgmSwitchValue = adminPanelObj.bgm;
          this.bgmVolume = adminPanelObj.bgmVolume;
          this.bgmCirculSwitchValue = adminPanelObj.bgmCircle?'circulate':'single';
          this.fixedTime = adminPanelObj.envTime;
          this.giftSwitchValue = adminPanelObj.unbox;
          this.luckySwitchValue = adminPanelObj.luckyDraw;
        }
      },
    }
  },
  computed: {
    wsConnect() {
      return this.$store.state.ws
    },
    larksr() {
      return this.$store.state.larksr
    },
    token() {
      return JSON.parse(this.$store.state.userInfo).token
    },
    // 人员列表总数
    memberTotalNum() {
      return this.speakerList.length - 0 + this.audienceList.length
    },
    // 举手总和
    memberHandupTotalNum() {
      return this.audienceDotNum + this.speakerDotNum;
    },
    // 背景音乐资源ID
    bgmResourceIndex() {
      return this.$store.state.bgmResourceIndex;
    },
  },
  methods: {
    iconClick(val) {
      if(val === this.curIcon) {
        this.curIcon = ''
      } else {
        this.curIcon = val;
      }
      if(val==='music'&&this.curIcon === val) {
        this.getBgmList();
      }
      if(val==='gift'&&this.curIcon === val) {
        this.getAwardList();
      }
      if(val==='person'&&this.curIcon === val) {
        this.memberList(1);
        this.memberList(3);
      }
    },
    // 将字段转换为后台需要的字段名称
    getAdminPanel() {
      let obj = {
        mainCamera: this.shotSwitchValue === 1? 1: 0,
        leftCamera: this.shotSwitchValue === 2? 1: 0,
        rightCamera: this.shotSwitchValue === 3? 1: 0,
        allMute: this.muteSwitchValue,
        fireworks: this.fireworksSwitchValue,
        bgm: this.bgmSwitchValue,
        bgmCircle: this.bgmCirculSwitchValue==='single'? 0: 1,
        bgmVolume: this.bgmVolume,
        currentTime: this.curTimeSwitchValue,
        fixedTime: this.curTimeSwitchValue===1? 0: 1,
        envTime: this.fixedTime,
        unbox: this.giftSwitchValue,
        luckyDraw: this.luckySwitchValue
      };
      return obj
    },
    // 左侧小屏切换镜头
    shotChange(val) {
      this.shotSwitchValue = val;
      const jsonData = JSON.stringify({
        scope: 1,
        type: 1,
        token: this.token,
        data: {
          value: this.shotSwitchValue ? this.shotSwitchValue : ''
        }
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 一键就座
    allSitDownClick() {
      const jsonData = JSON.stringify({
        scope: 1,
        type: 11,
        token: this.token,
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 全员静音
    allMuteClick(val) {
      this.muteSwitchValue = val;
      const jsonData = JSON.stringify({
        scope: 1,
        type: 7,
        token: this.token,
        data: {
          disable: this.muteSwitchValue
        }
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 烟花开关
    fireworksClick(val) {
      this.fireworksSwitchValue = val;
      const jsonData = JSON.stringify({
        scope: 1,
        type: 9,
        token: this.token,
        data: {
          status: this.fireworksSwitchValue,
        }
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 背景音乐
    bgmChangeClick() {
      let resourceList = [];
      if(this.bgmCirculSwitchValue==='single') {
        resourceList = [this.bgmList[0].mediaUrl]
      } else if(this.bgmCirculSwitchValue==='circulate'){
        resourceList = this.bgmList.map(item => {
          return item.mediaUrl
        })
      }
      const paramsData = {
        status: this.bgmSwitchValue,
        model: this.bgmCirculSwitchValue,
        bgmVolume: this.bgmVolume,
        resources: resourceList
      }
      const jsonData = JSON.stringify({
        scope: 1,
        type: 12,
        token: this.token,
        data: paramsData
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    bgmClick(val) {
      this.bgmSwitchValue = val;
      this.bgmChangeClick();
    },
    bgmCirculClick(val) {
      this.bgmCirculSwitchValue = val;
      this.bgmChangeClick();
    },
    // 调整背景音乐音量
    chagneBgmVolume(val) {
      this.bgmVolume = val;
      this.bgmChangeClick();
    },
    // 播放某一首音乐
    bgmPlayClick(curResourceIndex) {
      let resourceList = [];
      if(this.bgmCirculSwitchValue==='single') {
        resourceList = [this.bgmList[0].mediaUrl]
      } else if(this.bgmCirculSwitchValue==='circulate'){
        resourceList = this.bgmList.map(item => {
          return item.mediaUrl
        })
      }
      const paramsData = {
        model: this.bgmCirculSwitchValue,
        bgmVolume: this.bgmVolume,
        resources: resourceList
      }
      if(this.bgmResourceIndex === curResourceIndex) {
        paramsData.status = 3; // 恢复播放 
      } else {
        paramsData.status = 1; // 从开始播放一首新的音乐
        this.$store.commit('setBgmResourceIndex',curResourceIndex)
      }
        this.$set(this.bgmList[curResourceIndex], 'isPlay', true)
      if(curResourceIndex===0||curResourceIndex) {
        paramsData.currentResource = curResourceIndex
      }
      console.log(paramsData)
      const jsonData = JSON.stringify({
        scope: 1,
        type: 12,
        token: this.token,
        data: paramsData
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 暂停音乐
    bgmPauseClick(index) {
      this.$store.commit('setBgmResourceIndex',index)
      this.$set(this.bgmList[index], 'isPlay', false)
      let resourceList = [];
      if(this.bgmCirculSwitchValue==='single') {
        resourceList = [this.bgmList[0].mediaUrl]
      } else if(this.bgmCirculSwitchValue==='circulate'){
        resourceList = this.bgmList.map(item => {
          return item.mediaUrl
        })
      }
      const paramsData = {
        status: 2, //暂停
        model: this.bgmCirculSwitchValue,
        bgmVolume: this.bgmVolume,
        resources: resourceList
      }
      console.log(paramsData)
      const jsonData = JSON.stringify({
        scope: 1,
        type: 12,
        token: this.token,
        data: paramsData
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 获取背景音乐列表
    getBgmList() {
      const params = {
        activityId: this.$store.state.activityId,
        type: 6
      }
      getBgmList(params).then(res => {
        if(res && res.code === 1000) {
          this.bgmList = res.result?.records.map(item => {
            return Object.assign(item, {
              isPlay: false
            })
          })
        }
      })
    },
    // 环境设置
    curTimeChange(val) {
      this.curTimeSwitchValue = val;
      this.environmentSettine();
    },
    changeFixedTime(val,dir) {
      if(this.curTimeSwitchValue === 1) return;
      if(dir) { // 是否有第二个参数，用来判断是左边的6点还是右边的6点
        this.leftTime6 = dir;
      } else {
        this.leftTime6 = '';
      }
      this.fixedTime = val;
      this.environmentSettine();
    },
    environmentSettine() {
      let data = {
        weather: 'sunny', // rainy windy
        currentTime: this.curTimeSwitchValue,
      }
      if(this.curTimeSwitchValue === 0) {
        data.fixedTime = this.fixedTime
      }
      const jsonData = JSON.stringify({
        scope: 1,
        type: 10,
        token: this.token,
        data: data
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 抽奖
    luckySwitchChange(val) {
      this.luckySwitchValue = val;
      const jsonData = JSON.stringify({
        scope: 1,
        type: 14,
        token: this.token,
        data: {
          status: this.luckySwitchValue,
          url: window['config'].SING_AND_LUCKY_URL+'/luckyDraw?memberId='+this.$store.state.memberId+'&activityId='+this.$store.state.activityId+'&token='+this.token
        }
      })
      console.log('抽奖页面', window['config'].SING_AND_LUCKY_URL+'/luckyDraw?memberId='+this.$store.state.memberId+'&activityId='+this.$store.state.activityId+'&token='+this.token)
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 寻宝
    giftSwitchChange(val) {
      this.giftSwitchValue = val;
      const jsonData = JSON.stringify({
        scope: 1,
        type: 15,
        token: this.token,
        data: {
          status: this.giftSwitchValue,
        }
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    // 获取抽奖列表
    getAwardList() {
      const params = {
        activityId: this.$store.state.activityId,
      }
      getAwardList(params).then(res => {
        if(res && res.code === 1000) {
          this.tableData =  res.result.map(item => {
            return Object.assign(item, {
              number: 0
            })
          })
        }
      })
    },
    numberSub(row,index) {
      if(row.number === 0) return
      if(row.number>0) {
        this.$set(this.tableData[index], 'number', row.number--)
        console.log(this.tableData[index].number,row.number--)
      }
    },
    numberAdd(row,index) {
      if (row.number === row.leftoverNum) {
        return
      } else {
        this.$set(this.tableData[index], 'number', row.number++)
      }
    },
    numberChange(row, index) {
      if(row.number < 0) {
        this.$set(this.tableData[index], 'number', 0)
      } else if(row.number > row.leftoverNum) {
        this.$set(this.tableData[index], 'number', row.leftoverNum)
      }
    },
    // 抽奖
    luckyDraw(row) {
      const params = {
        activityId: this.$store.state.activityId,
        awardId: row.awardId,
        number: row.number
      }
      luckyDraw(params).then(res => {
        if(res && res.code === 1000) {
          console.log('中奖名单')
        //   "id":38287,// memberId
        // "name": "法外狂徒张三",// 中奖人姓名
        // "company":"精武门",// 公司名称
        // "avatar":""// 头像地址
        }
      })
    },
    // 人员列表搜索
    searchMemberList() {
      this.memberList(1);
      this.memberList(3);
    },
    searchMemberListCancle() {
      this.searchValue = '';
      this.memberList(1);
      this.memberList(3);
    },
    // 获取人员列表
    memberList(type) {
      const params = {
        activityId: this.activityId,
        roleType: type, //1. 观众，3. 演讲嘉宾
        condition: this.searchValue
      }
      getMemberList(params).then(res => {
        if(type === 1) {
          this.audienceList = res.result;
          this.audienceDotNum = 0;
          this.audienceList.map(item => {
            if(item.handUp===1) this.audienceDotNum++
          })
        } else if(type===3) {
          this.speakerList = res.result;
          this.speakerDotNum = 0;
          this.speakerList.map(item => {
            if(item.handUp===1) this.speakerDotNum++
          })
        }
        if(this.memberCurTab==='speaker') {
          this.personList = this.speakerList;
        } else if(this.memberCurTab==='audience') {
          this.personList = this.audienceList;
        }
      }) 
    },
    memberTabChange(val) {
      if(val.name==='speaker') {
        this.personList = this.speakerList;
      } else if(val.name === 'audience') {
        this.personList = this.audienceList;
      } else {
        this.personList = []
      }
    },
    // 踢出
    kickOut(row) {
      this.$confirm('踢除后不可参加本次活动，确定要踢掉该用户吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const jsonData = JSON.stringify({
          code: 5,
          type: 16,
          receiverUserId: row.memberId
        })
      this.wsConnect?.send(jsonData)
      }).catch(() => {
        this.$message.info('已取消踢除操作')        
      });
    },
    // 开始录制
    startRecode(e) {
      const jsonData = JSON.stringify({
        scope: 1,
        type: 13,
        token: this.token,
        data: {
          status: 1,
        }
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
      console.log('start recode');
      e.preventDefault();
      this.isRecode = true;
    },
    // 停止录制
    stopRecode(e) {
      const jsonData = JSON.stringify({
        scope: 1,
        type: 13,
        token: this.token,
        data: {
          status: 0,
        }
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
      console.log('stop recode');
      e.preventDefault();
      this.isRecode = false;
    },
    // 是否允许上台
    stageIconClick(status,memberId) {
      const params = {
        activityId: this.activityId,
        appointUserId: memberId,
        status: status
      }
      apiAllowStage(params).then(res => {
        if(res && res.code === 1000) {

        }
      })
    },
    
    // 是否允许投屏
    screenIconClick(status,memberId) {
      const params = {
        activityId: this.activityId,
        appointUserId: memberId,
        status: status
      }
      apiAllowScreen(params).then(res => {
        if(res && res.code === 1000) {
          
        }
      })
    },
    // 是否允许开麦
    voiceIconClick(status,memberId) {
      const jsonData = JSON.stringify({
        scope: 2,
        type: 19,
        token: this.token,
        data: {
          status: status,
        }
      })
      // this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6, receiverUserId: memberId, message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))

      // const params = {
      //   activityId: this.activityId,
      //   appointUserId: memberId,
      //   status: status
      // }
      // apiAllowVoice(params).then(res => {
      //   if(res && res.code === 1000) {
          
      //   }
      // })
    },
    // 光束
    lightIconClick(status,memberId) {
      const jsonData = JSON.stringify({
        scope: 2,
        type: 8,
        token: this.token,
        data: {
          status: status,
          memberId
        }
      })
      this.larksr?.sendTextToDataChannel(jsonData)
      var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
    },
    close(e) {
      // console.log(this.$refs.memberdiv)
      // if(this.curIcon !== '') {
      //   // 如果点击发生在当前组件内部，则不处理
      //   console.log('111')
        if(this.$refs.memberdiv) {
      //   console.log('222')
          if (this.$refs.memberdiv.contains(e.target)) {
            console.log('111')
            return;
          }
          // if(this.curIcon==='person') {
          //    console.log('222')
          // }
          // this.curIcon = ''
         
          // this.curIcon = '';
        }
      //   // this.curIcon = '';
      // }
      
    }
  },
  mounted() {
    //  document.body.addEventListener('click', this.close);
  }
}
</script>
<style lang='scss'>
.el-switch__core {
  width: 40px!important;
}
.hidden-span {
  display: inline-block;
  overflow:hidden;/*超出部分隐藏*/
	text-overflow:ellipsis;/*超出部分省略号表示*/
	white-space:nowrap;/*强制单行显示*/
	word-break:keep-all;
  width: 95px;
}
.bgm-tbody{
  &-tr {
