<template>
  <div ref="appContainer">
    <!-- @click="sountPlayout"
      @touchstart="onStart"
      @touchmove="onMove"
      @touchend="onEnd"
      @contextmenu.prevent=""
      @mouseleave="onLeave"
       -->
       
      <!-- @mousedown="onStart"
      @mousemove="onMove"
      @mouseup="onEnd" -->
    <div
      class="enter-appli noselect"
      v-if="showUi"
    >
      <!-- 左侧工具栏 -->
      <LeftBarVue
        @handDownEmit="handDownEmit"
      />
      <!-- 管理员面板 -->
      <AdminBarVue v-if="isAdmin" />
      <!-- 底部快捷键 -->
      <el-row type="flex" justify="center" class="bottom-bar">
        <div>
          <el-image v-if="hasMicAuth" @click="useMicClick(0)" :src="require('@/assets/images/bottom_mic.png')"></el-image>
          <el-image v-if="!hasMicAuth" @click="useMicClick(1)" :src="require('@/assets/images/bottom_mic_ban.png')"></el-image>
        </div>
        <div @click.prevent="screenShot">
          <el-image :src="require('@/assets/images/bottom_jieping.png')"></el-image>
        </div>
         <!-- v-if="scence === 4 && scenceStatus === 1" -->
        <div style="position: relative;">
          <el-tooltip class="item" effect="dark" placement="top" content="正在投屏，点击退出投屏">
            <el-image v-show="isShareScreenState&&hasScreenAuth" @click="disShareFn" :src="require('@/assets/images/bottom_touping_logout.png')"></el-image>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" placement="top" content="可投屏">
            <el-image v-show="hasScreenAuth&&!isShareScreenState" @click="shareFn" :src="require('@/assets/images/bottom_touping.png')"></el-image>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" placement="top" content="禁止投屏">
            <el-image v-show="!hasScreenAuth" :src="require('@/assets/images/bottom_touping_ban.png')"></el-image>
          </el-tooltip>
          <div v-if="showShareScreenTooltip&&!isAdmin" class="handle-btn-tooptip">
            <p>已允许投屏</p>
          </div>
          <div v-if="showShareScreenBanTooltip&&!isAdmin" class="handle-btn-tooptip">
            <p>已禁止投屏</p>
          </div>
        </div>
        <!-- v-if="scence === 4 && scenceStatus === 1" -->
        <div>
          <el-tooltip class="item" effect="dark" placement="top" content="正在使用最佳视角，点击后退出最佳视角">
            <el-image v-show="!isBestAngle" @click="bestAngleClick(0)" :src="require('@/assets/images/bottom_view.png')"></el-image>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" placement="top" content="点击开启最佳视角">
            <el-image v-show="isBestAngle" @click="bestAngleClick(1)" :src="require('@/assets/images/bottom_view_ban.png')"></el-image>
          </el-tooltip>
        </div>
         <!-- v-if="scence === 4 && scenceStatus === 1" -->
        <div>
          <el-tooltip class="item" effect="dark" placement="top" content="点击举手">
            <el-image @click="handsUpClick" v-show="!isHandsUp" :src="require('@/assets/images/bottom_fangxia.png')"></el-image>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" placement="top" content="正在申请发言，等待管理员审批，点击可放下">
            <el-image @click="handsDownClick" v-show="isHandsUp&&hasHandAuth" :src="require('@/assets/images/bottom_jushou.png')"></el-image>
          </el-tooltip>
          <div v-if="showHandUpTooltip&&!isAdmin" class="handle-btn-tooptip">
            <p>已允许发言</p>
          </div>
          <div v-if="showHandUpBanTooltip&&!isAdmin" class="handle-btn-tooptip">
            <p>已禁止发言</p>
          </div>
        </div>
        <div class="action">
          <el-image class="action-div" v-if="showAction" @click="actionIconClick" style="width: 100%;height:100%;" :src="require('@/assets/images/action_cur.png')"></el-image>
          <el-image class="action-div" v-else @click="actionIconClick" style="width: 100%;height:100%;" :src="require('@/assets/images/action.png')"></el-image>
          
          <div class="action-div" @mouseover="isHiCur=true" @mouseleave="isHiCur=false" v-show="showAction" @click.prevent="actionClick('hi')">
            <el-image v-show="isHiCur" style="width:60px;height:60px" :src="require('@/assets/images/hi_cur.png')"></el-image>
            <el-image v-show="!isHiCur" style="width:60px;height:60px" :src="require('@/assets/images/hi.png')"></el-image>
          </div>
          <div class="action-div" @mouseover="isClapCur=true" @mouseleave="isClapCur=false" v-show="showAction" @click.prevent="actionClick('applaud')">
            <el-image v-show="isClapCur" style="width:60px;height:60px" :src="require('@/assets/images/clap_cur.png')"></el-image>
            <el-image v-show="!isClapCur" style="width:60px;height:60px" :src="require('@/assets/images/clap.png')"></el-image>
          </div>
          <div class="action-div" @mouseover="isCheerCur=true" @mouseleave="isCheerCur=false" v-show="showAction" @click.prevent="actionClick('jubilate')">
            <el-image v-show="isCheerCur" style="width:60px;height:60px" :src="require('@/assets/images/cheer_cur.png')"></el-image>
            <el-image v-show="!isCheerCur" style="width:60px;height:60px" :src="require('@/assets/images/cheer.png')"></el-image>
          </div>
          <div class="action-div" @mouseover="isJumpCur=true" @mouseleave="isJumpCur=false" v-show="showAction" @click.prevent="actionClick('jump')">
            <el-image v-show="isJumpCur" style="width:60px;height:60px" :src="require('@/assets/images/jump_cur.png')"></el-image>
            <el-image v-show="!isJumpCur" style="width:60px;height:60px" :src="require('@/assets/images/jump.png')"></el-image>
          </div>
        </div>
      </el-row>
      <!-- 共享房间 -->
      <div v-if="roomClient" style="margin: 0 auto;z-index: 999999;">
        <peer-view v-if="me" :info="me" :isMe="true" :audioTrack="me.audioTrack" :videoTrack="me.videoTrack"
                  @muteMic="roomClient.muteMic()"
                  @unmuteMic="roomClient.unmuteMic()"
                  @disableWebcam="roomClient.disableWebcam()"
                  @enableWebcam="roomClient.enableWebcam()"
                  @disableShare="roomClient.disableShare()"
                  @enableShare="roomClient.enableShare()"
        ></peer-view>
        <peer-view v-for="(item,index) in others" :key="index" :info="item" :audioTrack="item.audioTrack" :videoTrack="item.videoTrack"></peer-view>
      </div>
      <!-- 截屏 -->
      <DialogVue :dialogVisible="screenDialogVisible" @closeDialog="screenCloseDialogFn" >
        <div class="screen-dialog">
          <img v-if="url" style="width:300px;height:188px;crossOrigin:anonymous;-webkit-touch-callout: default;" :src="url"/>
          <el-row type="flex" justify="center">
            <!-- <div v-if="isMobile">
              <div @click.prevent="shareGroup">
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#icon-weixin2"></use>
                </svg>
              </div>
              <p>微信</p>
            </div> 
            <div v-if="isMobile">
              <div @click.prevent="shareGroupwechat">
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#icon-pengyouquan"></use>
                </svg>
              </div>
              <p>微信朋友圈</p>
            </div>   -->
            <p v-if="isMobile" style="color:#fff;">长按图片保存截图</p>
            <div v-else>
              <div @click.prevent="downloadScreenImg">
                <span class="iconfont icon-xiazai-wenjianxiazai-16" style="font-size: 22px;"></span>
              </div>
              <p>保存图片</p>
            </div>
          </el-row>
        </div>
      </DialogVue>
    </div>
  </div>
</template>
<script>
import DialogVue from './components/Dialog.vue'
import RoomClient from "./js/RoomClient";
import PeerView from "./PeerView";
import LeftBarVue from './LeftBar.vue'
import AdminBarVue from "./AdminBar.vue";
import { LarkSR, LoadAppliParamsFromUrl } from "larksr_websdk";
import { apiAuthScreen, apiAuthVoice, sendTaskId, apiScreenCallback } from '@/apis/api'
import { mapGetters } from 'vuex';

const DataChannelType = {
  // 客⼾端⼤⼩
  ClientSize: 3,
};
export default {
  components: { 
    PeerView,
    LeftBarVue,
    AdminBarVue,
    DialogVue
  },
  data() {
    return {
      roomClient: {},
      roomId:this.getUUIDStr(5,16),//房间号
      peerId:this.getUUIDStr(8,16),//用户id
      me:null,
      others:[],
      started: false, // 旋转角度是否开始
      AngleValue: 0, // 角度
      mobPreScreenX: 0, // 移动端计算滑动点需记住上一次坐标点
      savedCamera: {
        angle: 0,
      },
      showUi: true,
      larksr: null,
      isShowMic: true, // 是否允许使用麦克风
      isBestAngle: true, // 是否是最佳视角
      isHandsUp: false, // 是否举手
      showAction: false, // 展示动作面板
      screenDialogVisible: false, // 截屏弹窗
      url: '',
      screenShotInfo: {},
      scence: null, // 场景值
      scenceStatus: null, //场景状态：进入、离开
      handsUpTimer: null, // 举手的timer 定时15秒
      isHiCur: false,
      isClapCur: false,
      isCheerCur: false,
      isJumpCur: false
    }
  },
  methods: {
    // 分享到微信
    shareGroupwechat() {
      if(navigator.share) {
        const shareOpts = {
          text: '平行云境.',
          url: 'https://pxy-activity.oss-cn-beijing.aliyuncs.com/8W7GI1Vl/bg/65ab818fb929ffd16018f4f65980ee85_1660706287885.jpeg',
        };
        navigator.share(shareOpts);
      }
    },
    // 分享到朋友圈
    shareGroup() {
      const shareOpts = {
        files: this.url,
        title: '平行云境'
      };
      navigator.share(shareOpts).then(res => {
        console.log('分享成功')
      }).catch(err => {
        console.log('分享失败', err)
      })
    },
    // 旋转角度start
    onStart() {
      this.started = true;
    },
    // 旋转角度end
    onEnd() {
      this.started = false;
      this.mobPreScreenX = 0;
    },
    // 旋转角度move
    onMove(e) {
      if (!this.started) {
        return;
      }
      let value,screenXDiff;
      if (this.isMobile) {
        if (this.mobPreScreenX === 0) {
          this.mobPreScreenX = e.changedTouches[0].screenX;
          return false;
        }
        screenXDiff = ((e.changedTouches[0].screenX - this.mobPreScreenX) / 300) * 100;
        this.mobPreScreenX = e.changedTouches[0].screenX;
      } else {
        screenXDiff = (e.movementX / 300) * 100;
      }
      value = this.AngleValue - screenXDiff;
      this.AngleValue = value;
      this.changeAngle(value);
    },
    // 切换摄像机角度
    changeAngle(angle) {
      this.savedCamera.angle = angle;
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          type: 2,
          data: this.savedCamera,
        })
      );
    },
    // 麦克风点击
    useMicClick(status) {
      if(status === 0) {
        const jsonData = JSON.stringify({
          scope: 2,
          type: 19,
          token: this.token,
          data: {
            status: status,
          }
        })
        this.larksr?.sendTextToDataChannel(jsonData)
        var message = {code:6,message:jsonData}
        this.wsConnect?.send(JSON.stringify(message))
        this.$store.commit('setHasMicAuth', false);
      } else if(status === 1) {
        // 获取麦克风权限
        const params = {
          activityId: this.localActivityId
        }
        apiAuthVoice(params).then(res => {
          if(res.code === 1000) {// 允许开麦
            const jsonData = JSON.stringify({
              scope: 2,
              type: 19,
              token: this.token,
              data: {
                status: status,
              }
            })
            this.larksr?.sendTextToDataChannel(jsonData)
            this.$store.commit('setHasMicAuth', true);
            var message = {code:6,message:jsonData}
            this.wsConnect?.send(JSON.stringify(message))
          } else {
            // 没有权限-直接关闭麦克风
            this.$store.commit('setHasMicAuth', false);
          }
        })
      }
    },
    // 投屏
    async shareFn() {
      console.log('_mediaSoupDevice', this.roomClient._mediaSoupDevice)
      if(!this.roomClient._mediaSoupDevice) {
        this.$message.warning('mediaSoup service connect faild');
        return
      }
      // 投屏校验
      const params = {
        activityId: this.localActivityId
      }
      let canShareScreen = false // 不允许投屏 500
      await apiAuthScreen(params).then(res => {
        if(res) {
          if(res.code === 1000) {// 允许投屏
            canShareScreen = true
          }if(res.code === 500) {
            canShareScreen = false
          }
        } else {
          canShareScreen = false
        }
        // 允许投屏
        if(canShareScreen) {
          if(!this.$route.query.roomId) {
            this.$router.push({
              path: '/appli/enter',
              query: { ...this.$route.query, roomId: this.roomId,}
            })
          }
          this.roomClient.enableShare();
        } else {// 不允许投屏
         this.$store.commit('setIsShareScreen', false);
         this.$store.commit('setHasScreenAuth', false)
        }
      })
    },
    // 结束投屏
    disShareFn() {
      this.roomClient.disableShare();
      const params = {
        activityId: this.$store.state.activityId,
        status: 1
      }
      apiScreenCallback(params).then(res => {
        if(res && res.code === 1000) {
          
        }
      })
      // this.isShareScreen = true;
      // this.$store.commit('setIsShareScreen', false);
    },
    // 截屏
    screenShot() {
      this.larksr.captrueFrame(new Date().getTime());
    },
    // 截屏弹窗关闭
    screenCloseDialogFn() {
      this.url = '';
      this.screenShotInfo = {};
      this.screenDialogVisible = false;
    },
    // 保存屏幕截图
    downloadScreenImg() {
      this.downloadFile(this.screenShotInfo);
      this.screenCloseDialogFn();
    },
    dataURLtoBlob(dataurl) {
      var arr = dataurl.split(',');
        //注意base64的最后面中括号和引号是不转译的   
        var _arr = arr[1].substring(0,arr[1].length-2);
        var mime = arr[0].match(/:(.*?);/)[1],
          bstr =atob(_arr),
          n = bstr.length,
          u8arr = new Uint8Array(n);
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n);
        }
        return new Blob([u8arr], {
          type: mime
        });
    },
    downloadFile(saveInfo) {
      const element = document.createElement('a');
      element.style.display = 'none';
      for (const key in saveInfo) {
        element.setAttribute(key, saveInfo[key]);
      }
      document.body.appendChild(element);
      element.click();
      setTimeout(() => {
        document.body.removeChild(element);
      }, 300)
    },
    // 最佳视角点击
    bestAngleClick(status) {
      // status 0 - 不显示， 1 - 显示;
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          scope: 2,
          type: 6,
          token: JSON.parse(this.$store.state.userInfo).token,
          data: {
            status: status
          }
        })
      )
      this.isBestAngle = !this.isBestAngle;
    },
    // 举手
    handsUpClick() {
      if(!this.isHandsUp) {
        if(this.handsUpTimer) clearTimeout(this.handsUpTimer);
        this.handsUpTimer = setTimeout(this.handsDownClick,15000);
      }
       const jsonData = JSON.stringify({
          scope: 2,
          type: 3,
          token: JSON.parse(this.$store.state.userInfo).token,
          data: {
            status: 1
          }
        })
      // status 0 - 放下 1 - 举手;
      this.larksr?.sendTextToDataChannel(jsonData)
       var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
      this.isHandsUp = true;
      this.$store.commit('setHasHandAuth', true)
    },
    // 放下举手
    handsDownClick() {
      const jsonData = JSON.stringify({
          scope: 2,
          type: 3,
          token: JSON.parse(this.$store.state.userInfo).token,
          data: {
            status: 0
          }
        })
      // status 0 - 放下 1 - 举手;
      this.larksr?.sendTextToDataChannel(jsonData)
       var message = {code:6,message:jsonData}
      this.wsConnect?.send(JSON.stringify(message))
      this.isHandsUp = false;
    },
    // 举手图标修改为放下
    handDownEmit() {
      const jsonData = JSON.stringify({
          scope: 2,
          type: 3,
          token: JSON.parse(this.$store.state.userInfo).token,
          data: {
            status: 0
          }
        })
      // status 0 - 放下 1 - 举手;
      this.larksr?.sendTextToDataChannel(jsonData)
      //  var message = {code:6,message:jsonData}
      // this.wsConnect?.send(JSON.stringify(message))
      this.isHandsUp = false;
    },
    // 动作
    actionClick(type) {
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          scope: 2,
          type: 2,
          token: JSON.parse(this.$store.state.userInfo).token,
          data: {
            value: type
          }
        })
      )
    },
    actionIconClick() {
      this.showAction = !this.showAction;
    },
    getUUIDStr(len, radix) {
        let chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
        let uuid = [],i;
        radix = radix || chars.length;
        if (len) {
          for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
        } else {
          let r;
          uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
          uuid[14] = '4';
          for (i = 0; i < 36; i++) {
            if (!uuid[i]) {
              r = 0 | Math.random() * 16;
              uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
            }
          }
        }
        return uuid.join('');
    },
    // 客户端大小
    clientSize(width, height) {
      this.larksr?.sendTextToDataChannel(
        JSON.stringify({
          type: DataChannelType.ClientSize,
          data: {
            platform: "win32",
            width,
            height,
            scale: 1,
          },
        })
      );
    },
    // 进入应用成功、给后台传参
    sendConnectSuccess(taskId) {
      const params = {
        activityId: this.$store.state.activityId || localStorage.getItem('activityId'),
        memberId: this.$store.state.memberId || localStorage.getItem('memberId'),
        taskId: taskId
      }
      sendTaskId(params).then(res => {
        if(res && res.code === 1000) {

        }
      })
    },
    // 进入应用
    connectAppli() {
      // 直接调用进入应用接口创建实例，自动配置连接云端资源
      this.larksr = new LarkSR({
        rootElement: this.$refs["appContainer"],
        // 服务器地址
        serverAddress: window['config'].LARKXR_SERVER_ADDRESS,
        scaleMode: "fill_clip",
        // 0 -》 用户手动触发, 1 -》 首次点击进入触发, 2 -》 每次点击触发
        fullScreenMode: 0,
        // 测试载入背景图
        loadingBgUrl: "https://home-oss.pingxingyun.com/homePage/bg.jpg",
        // 手机端是否强制横屏
        mobileForceLandscape: true,
        authCode: window['config'].SDK_AUTH_CODE,
        // authCode: '6b09421012994a2bba959b556fc2b78f',
        // authCode: '5966b6569bec4f879322a8c91f2f2cd4', // 55环境
        frameRate: 60,
        codeRate: 8000,
      });
      if(this.larksr) {
        this.$store.commit('setLarksr', this.larksr)
      }
      console.log('larksr', this.larksr, LoadAppliParamsFromUrl());
      // 关闭触摸中间的点
      this.larksr.isEnableTouchPonit = false;
      this.larksr.connect({ 
        appliId: window['config'].LARKXR_APPLI_ID,
      }).then(() => {

      }).catch((e) => {
        console.log("load appli failed", e);
      });
      this.larksr.on("connect", (e) => {
        console.log("LarkSRClientEvent CONNECT", e);
      });
      // TASK 创建成功，返回 Task 相关信息
      this.larksr.on("taskcreatesuccess", (e) => {
        this.sendConnectSuccess(e.data.taskid)
      });
      this.larksr.on("gotremotesteam", (e) => {
        console.log("LarkSRClientEvent gotremotesteam", e);
      });
      this.larksr.on("meidaloaded", (e) => {
        console.log("LarkSRClientEvent meidaloaded", e);
        this.showUi = true;
        // 打开麦克风
        this.larksr.openAudio() .then((res) => {
          console.log("open audio success", res);
          // this.audioTrackEnable = this.audioTrack.enabled;
        }).catch((e) => {
          console.warn("open audio failed.", e);
          const u = navigator.userAgent; 
          const isIos = u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) || (u.indexOf('Safari') != -1 && u.indexOf("Version") != -1 && 'ontouchend' in document);
          if (isIos && (/micromessenger/i).test(u)) {
            this.$message.warning("iOS 微信浏览器不支持打开麦克风，请使用Safari访问音频功能");
          } 
          // else if (window.location.href.indexOf("https") == -1) {
          //   this.$message.warning("请通过 HTTPS 连接访问音频功能");
          // } else {
          //   this.$message.error(JSON.stringify(e));
          // }
        });
      });
      this.larksr.on("mediaplaysuccess", (e) => {
        console.log("LarkSRClientEvent mediaplaysuccess", e);
      });
      this.larksr.on("mediaplayfailed", (e) => {
        console.log("LarkSRClientEvent mediaplayfailed", e);
        this.isMediaplayfailed = true;
      });
      this.larksr.on("meidaplaymute", (e) => {
        console.log("LarkSRClientEvent meidaplaymute", e);
      });
      // 是否是预启动
      this.larksr.on("loginsuccess", (e) => {
        console.log("LarkSRClientEvent login success isPrestart", e.data.isPrestart);
      });
      this.larksr.on("datachannelopen", (e) => {
        console.log("datachannel open", e);
        console.log(this.$store.state)
        // 传递给应用身份信息
        this.larksr?.sendTextToDataChannel(
          JSON.stringify({
            scope: 2,
            type: 0,
            token: JSON.parse(this.$store.state.userInfo).token,
            data: {
              memberId: this.$store.state.memberId,
              activityId: this.$store.state.activityId,
              apiUrl: window['config'].ZEUS_API_URL,
              signUrl: window['config'].SING_AND_LUCKY_URL+'/?memberId='+this.$store.state.memberId+'&activityId='+this.$store.state.activityId+'&token='+JSON.parse(this.$store.state.userInfo).token
            }
          })
        )
      console.log('签到页面',window['config'].SING_AND_LUCKY_URL+'/?memberId='+this.$store.state.memberId+'&activityId='+this.$store.state.activityId+'&token='+JSON.parse(this.$store.state.userInfo).token)
      });
      this.larksr.on('aivoiceerror', (e) => {
        alert(JSON.stringify(e.data));
      });
      this.larksr.on('aivoicestatus', (e) => {
        console.log('aivoicestatus', e);
      });
      this.larksr.on('aivoiceasrresult', (e) => {
        console.log('aivoiceasrresult', e.data);
      });
      this.larksr.on('aivoicedmresult', (e) => {
        try {
          let res = JSON.parse(e.data.text);
          console.log('aivoicedmresult', res);
        } catch(e) {
          console.warn('parse aivoicedmresult failed', e.data);
        }
      });
      this.larksr.on("datachanneltext", (e) => {
        try {
          const cmd = JSON.parse(e.data);
          console.log('datachannel cmd', cmd);
          // TODO:数据通道接收消息
          if(cmd.type===1) { // 接收场景值信息
            this.$store.commit('setPersonScenceInfo',JSON.stringify(cmd.data))
          }else if(cmd.type===2) {// 背景音乐循环播放时，播放下一首通知播放的index
            this.$store.commit('setBgmResourceIndex',cmd.data.currentResource)
          }
        } catch(ext) {
          console.log('parse datachannel json cmd failed', e.data);
        }
      });
      this.larksr.on("error", (e) => {
        console.error("LarkSRClientEvent error", e);
      });
      this.larksr.on("operatetimeout", (e) => { // 无操作超时时触发
        console.log('operatetimeout',e)
      })
      this.larksr.on("close", (e) => { // 连接关闭时触发
        console.log('close',e)
      })
      this.larksr.on("appclose", (e) => { // 云端应用关闭，但连接未关闭
        console.log('appclose',e)
      })
      this.larksr.on("info", (e) => {
        console.log("LarkSRClientEvent info", e);
      });
      // 监听到截屏成功事件
      this.larksr.on("captureframe", (e) => {
        const capture = e.data.base64;
        const saveInfo = {
          'download': `截屏.png`,
          'href': capture
        };
        this.url = capture;
        this.screenShotInfo = saveInfo;
        this.screenDialogVisible = true;
      });
    },
  },
  computed:{
    // 是否允许使用麦克风
    hasMicAuth() {
      return this.$store.state.hasMicAuth;
    },
    // 是否允许投屏
    hasScreenAuth() {
      return this.$store.state.hasScreenAuth;
    },
    // 是否举手
    hasHandAuth() {
      return this.$store.state.hasHandAuth;
    },
    producerChange(){
      if(this.roomClient){
        const {_micProducer,_webcamProducer,_shareProducer,_device,_peerId} = this.roomClient;
        return { _micProducer,_webcamProducer,_shareProducer,_device,_peerId }
      }
      return null;
    },
    consumerChange(){
      if(this.roomClient){
        return this.roomClient._consumers;
      }
      return null;
    },
    // 活动id
    localActivityId() {
      return this.$store.state.activityId
    },
    // 是否正在投屏
    isShareScreenState() {
      return this.$store.state.isShareScreen
    },
    // 是否是手机端
    isMobile() {
      return this.$store.state.isMobile
    },
    // 是否是管理员角色
    isAdmin() {
      return this.$store.state.isAdminAuth
    },
    wsConnect() {
      return this.$store.state.ws
    },
    ...mapGetters([
      'showShareScreenTooltip',
      'showShareScreenBanTooltip',
      'showHandUpTooltip',
      'showHandUpBanTooltip',
      'hasHandAuth'
    ])
  },
  watch:{
    'roomClient': {
      handler(newValue) {
        console.log('roomClient newValue', newValue)
        let _consumers = newValue._consumers
        if(_consumers && _consumers.size>0){
        const res= {};
        _consumers.forEach(item=>{
          const {appData:{device,peerId,socketId},kind,track}=item;
          console.log(item)
          if(res[peerId]){
            if(kind=='audio'){
              res[peerId].audioTrack=track;
              res[peerId].audioConsumer=item;
            }else{
              res[peerId].videoTrack=track;
              res[peerId].videoConsumer=item;
            }
          }else{
            res[peerId]={
              device,
              peerId,
              audioTrack:kind=='audio'?track:null,
              videoTrack:kind=='video'?track:null,
              audioConsumer:kind=='audio'?item:null,
              videoConsumer:kind=='video'?item:null
            };
          }
        });
        let others=[];
        for(let key in res){
          others.push(res[key]);
        }
        this.others=others;
        console.log('this.others', this.others)
      }else{
        this.others=[];
      }
      console.log(this.others)
      },
      immediate: true,
      deep: true
    },
    producerChange(newValue,oldValue){
      const { _micProducer,_webcamProducer,_shareProducer,_device,_peerId }=newValue;
      if(_webcamProducer||_shareProducer){
        this.me={
          // audioTrack:_micProducer.track,
          videoTrack:_webcamProducer?_webcamProducer.track:_shareProducer.track,
          audioProducer:_micProducer,
          videoProducer:_webcamProducer?_webcamProducer:_shareProducer,
          device:_device,
          peerId:_peerId
        };
        console.log(this.me.videoTrack,'************************')
      }else{
        this.me={
          // audioTrack:_micProducer.track,
          videoTrack:null,
          audioProducer:_micProducer,
          videoProducer:null,
          device:_device,
          peerId:_peerId
        };
      }
    },
    consumerChange(newValue,oldValue){
      // console.log('监听到con&&&&&&&&&&&&&&&&&&&&&&&', newValue)
      // const { _consumersLength,_consumers }=newValue;
      // if(_consumers.size>0){
      //   const res= {};
      //   _consumers.forEach(item=>{
      //     const {appData:{device,peerId,socketId},kind,track}=item;
      //     if(res[peerId]){
      //       if(kind=='audio'){
      //         res[peerId].audioTrack=track;
      //         res[peerId].audioConsumer=item;
      //       }else{
      //         res[peerId].videoTrack=track;
      //         res[peerId].videoConsumer=item;
      //       }
      //     }else{
      //       res[peerId]={
      //         device,
      //         peerId,
      //         audioTrack:kind=='audio'?track:null,
      //         videoTrack:kind=='video'?track:null,
      //         audioConsumer:kind=='audio'?item:null,
      //         videoConsumer:kind=='video'?item:null
      //       };
      //     }
      //   });
      //   let others=[];
      //   for(let key in res){
      //     others.push(res[key]);
      //   }
      //   this.others=others;
      //   console.log('this.others', this.others)
      // }else{
      //   this.others=[];
      // }
      // console.log(this.others)
    },
    'this.$store.state.personScenceInfo': {
      handler(newValue) {
        if(newValue) {
          const data = JSON.parse(newValue);
          this.scence = data.loc;
          this.scenceStatus = data.act;
        }
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    // 连接应用
    this.connectAppli()
    // 投屏 连接websocket
    let roomId = '';
    const peerId = this.peerId;
    let queryInfo = this.$route.query;
    if(queryInfo.roomId) {
      roomId = queryInfo.roomId;
    } else {
      roomId = this.roomId;
    }
    let roomClient = new RoomClient({roomId,peerId});
    roomClient.on("close",(data)=>{
      console.log('close', data)
    })
    roomClient.joinRoom();
    this.roomClient=roomClient;
    this.$store.commit('setRoomClient',roomClient);
    
  },
  beforeUnmount() {
    this.larksr.app.disConnect();
    this.larksr = null;
    this.$store.commit('setLarksr', null)
    if(this.handsUpTimer) clearTimeout(this.handsUpTimer);
    this.roomClient.close('页面销毁');
    this.$store.commit('setRoomClient',null);
  }
}
</script>
